import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from "@ngx-translate/core";
import { rawData, syntheticData } from "../../util/constants";

@Component({
  selector: 'analysis-segment',
  templateUrl: 'analysis-segment.html'
})
export class AnalysisSegmentComponent implements OnInit {

  @Input() pageTitle: string;
  @Input() tab: { name: string, value: number };
  @Output() switch = new EventEmitter<any>();

  private tabName: string;

  private rawData = rawData;
  private syntheticData = syntheticData;

  private tabRawData: string;
  private tabSyntheticData: string;

  constructor(private translate: TranslateService) {
    this.translate.get('tabs.title.rawData').take(1).subscribe(value => this.tabRawData = value);
    this.translate.get('tabs.title.selectedData').take(1).subscribe(value => this.tabSyntheticData = value);
  }

  ngOnInit(): void {
    this.tabName = this.tab.name;
  }

  selectTab(isNormalText: boolean) {
    this.switch.emit({
      switch: isNormalText,
    });
  }
}



