import { AngularFirestore } from "angularfire2/firestore";
import { Transcription } from "../../model/Transcription";
import { Observable } from "rxjs/Observable";
import { HttpClient } from '@angular/common/http';
import * as firebase from "firebase/app";
import "rxjs/add/operator/first";
import { Injectable } from "@angular/core";
import { pathTranscriptions, replaceId } from "../../util/constants";
import "firebase/storage";
import { Utils } from "../../util/utils";
import { RecordService } from "../recording/record.service";
import DocumentReference = firebase.firestore.DocumentReference;

@Injectable()
export class TranscriptionsService {

  constructor(private firestore: AngularFirestore, private httpClient: HttpClient, private recordService: RecordService) {
  }

  public create(transcription: Transcription, idProject: string): Promise<DocumentReference> {
    return this.firestore.collection<Transcription>(TranscriptionsService.getPath(idProject)).add({...transcription});
  }

  public getTranscriptionById(idProject: string, idTranscription: string): Observable<Transcription> {
    return this.firestore.doc<Transcription>(`${TranscriptionsService.getPath(idProject)}/${idTranscription}`).valueChanges().share();
  }

  public update(idProject: string, idTranscription: string, data: Partial<Transcription>): Promise<void> {
    return this.firestore.doc<Transcription>(`${TranscriptionsService.getPath(idProject)}/${idTranscription}`).update(data);
  }

  public remove(idProject: string, idTranscription: string): Promise<void> {
    return this.firestore.doc<Transcription>(`${TranscriptionsService.getPath(idProject)}/${idTranscription}`).delete();
  }

  public saveAudioBlob(audioBlob: Blob, metadata: any): Promise<any> {
    let fileReader = new FileReader();

    return this.recordService.promiseFileReaderURL(audioBlob, fileReader).then(() => {
      const promises = [];
      promises.push(firebase.storage().ref(metadata.customMetadata.audioId).putString(`{"data": "${fileReader.result}"}`, 'raw', metadata));
      const temp = Utils.audioPath(metadata.customMetadata.projectId);
      promises.push(firebase.storage().ref(temp).put(audioBlob, metadata));
      return Promise.all(promises);
    });
  }

  public static getAudioByRef(refToAudio: string) {
    return firebase.storage().ref().child(refToAudio).getDownloadURL();
  }

  public getAudioBlobByUrl(url: string): Promise<any> {
    return this.httpClient.get(url).toPromise();
  }

  private static getPath(idProj: string): string {
    return pathTranscriptions.replace(replaceId, idProj);
  }
}



