import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavController, NavParams, Platform } from 'ionic-angular';
import { TranscriptionsService } from "../../providers/persistence/transcriptions.service";
import { TranslateService } from "@ngx-translate/core";
import { Subscription } from "rxjs/Subscription";
import { Project} from "../../model/Project";
import { Observable } from "rxjs/Observable";
import { rawData } from "../../util/constants";
import { InterviewsService } from "../../providers/persistence/interviews.service";
import { TimerComponent } from "../../components/timer/app.component";
import { QuestionService } from "../../providers/persistence/question.service";
import { RecordService } from "../../providers/recording/record.service";
import { Utils } from "../../util/utils";
import { Transcription } from "../../model/Transcription";
import { Tag } from "../../model/Tag";
import { Interview } from "../../model/Interview";
import { Note } from "../../model/Note";

/**
 * Controller of analysis text page.
 * This page display the recorded text.
 */
@Component({
  selector: 'page-analysis-text',
  templateUrl: 'analysis-text.html',
})
export class AnalysisTextPage implements OnInit, OnDestroy {

  private subscriptions: Subscription[];

  private playTimerComponent: TimerComponent;

  private interview: Observable<Interview>;
  private transcription: Observable<Transcription>;
  private intro: Observable<Note[]>;
  private tags: Array<Tag>;
  private filteredTags: Array<Tag>;

  private project: Project;
  private interviewId: string;
  private transcriptionId: string;

  private audioPlayer;

  private subTab = rawData;

  private playTimer: number;
  private seconds: number;
  private minutes: number;
  private totalTimer: number;
  private inPause: boolean;

  private currentPlainText: string;
  private currentCutText: Array<any>;
  private isRawText: boolean;
  private isAudio: boolean;

  private draggable: boolean;

  private questionTimeTag: string;

  /**
   * @name constructor
   * @desc Constructor init our values.
   * @param {NavController} navCtrl
   * @param {NavParams} navParams
   * @param {TranslateService} translate
   * @param {TranscriptionsService} transcriptionsService
   * @param {QuestionService} questionService
   * @param {InterviewsService} interviewsService
   * @param {RecordService} recordService
   * @param {Platform} platform
   * @memberOf AnalysisTextPage
   */
  constructor(private navCtrl: NavController, private navParams: NavParams,
              private translate: TranslateService, private transcriptionsService: TranscriptionsService,
              private questionService: QuestionService, private interviewsService: InterviewsService,
              private recordService: RecordService, private platform: Platform) {
    this.project = this.navParams.get("project");
    this.interviewId = this.navParams.get("interviewId");

    this.playTimer = 0;
    this.seconds = 0;
    this.minutes = 0;
    this.inPause = true;

    this.isRawText = true;

    this.draggable = false;

    this.totalTimer = 0;

    this.questionTimeTag = "questionTime";
  }

  /**
   * @name ngOnInit
   * @desc On init, subscribe to observable.
   * @memberOf AnalysisTextPage
   */
  public ngOnInit() {
    // Get data
    this.interview = this.interviewsService.getInterviewById(this.project.id, this.interviewId);
    this.intro = this.questionService.getNote(this.project.id);

    this.subscriptions = new Array<Subscription>();
    // Subcriptions

    this.subscriptions.push(this.interview.subscribe((interview) => {
      this.tags = interview.tags;
      this.filteredTags = interview.tags.filter((tag) => { return tag.type !== this.questionTimeTag; });

      // Get the text if ref exists
      if(interview.refToText) {
        // Get the transcription
        this.transcription = this.transcriptionsService.getTranscriptionById(this.project.id, interview.refToText);

        // If subscription already exists we need to override it
        if (this.subscriptions[1]) this.subscriptions[1].unsubscribe();
        this.subscriptions[1] = this.transcription.subscribe((transcription) => {
          this.currentPlainText = transcription.plainText;
          this.currentCutText = transcription.textByQuestions;

          // If tab of cut text not null, subscribe to it and memorise data
          if(this.currentCutText) {
            this.currentCutText.forEach((q) => {
              this.subscriptions.push(this.questionService.getQuestionById(this.project.id, q.refToQuestion).subscribe((data) => {
                q.questionName = data.question;
                q.order = data.order;
              }));
            });

            this.currentCutText = this.currentCutText.sort((x, y) => { return x.order - y.order; });
          }
        });

        // Remember the ref
        this.transcriptionId = interview.refToText;
      } else {
        this.transcription = null;
      }

      // Get the audio if ref exists
      if(interview.refToAudio) {
        this.isAudio = true;

        // Get the file as a promise
        TranscriptionsService.getAudioByRef(interview.refToAudio).then((url) => {
          this.transcriptionsService.getAudioBlobByUrl(url).then((audioBlob) => {
            const blob = Utils.b64toBlob(audioBlob.data.slice(22), '', 512);
            this.audioPlayer = new Audio(window.URL.createObjectURL(blob));
            this.totalTimer = Math.floor(interview.duration * 1000);
          }).catch((err) => Utils.error(err));
        });

      } else {
        // Hiding the player / timeline
        this.isAudio = false;
        this.totalTimer = 0;
      }
    }));

    this.subscriptions.push(this.intro.subscribe());
  }

  /**
   * @name ngOnDestroy
   * @desc On destroy, unsubscribe to all subscribed observable.
   * @memberOf AnalysisTextPage
   */
  public ngOnDestroy() {
    this.subscriptions.forEach((e) => e.unsubscribe());
  }

  /**
   * @name getTimer
   * @desc Get the timer if sent by component to us.
   * @param {} event: the event with sended data.
   * @memberOf AnalysisTextPage
   */
  public getTimer(event): void {
    this.playTimerComponent = event.timerComponent;
  }

  /**
   * @name updateTotalTimstamp
   * @desc Update timestamp each time component refresh it.
   * @param {} event: the event with sended data.
   * @memberOf AnalysisTextPage
   */
  public updateTotalTimestamp(event): void {
    this.playTimer = event.timestampTotal;
    this.seconds = (event.timestampTotal / 1000) % 60;
    this.minutes = (event.timestampTotal / 1000) / 60;

    // Prevent to exceed totalTimer value => stop the player
    if(this.playTimer >= this.totalTimer) {
      this.playTimerComponent.stop();
      this.startStopPlaying({value: false});
      this.playTimer = this.totalTimer;
    }
  }

  /**
   * @name startStopPlaying
   * @desc Start/Stop playing when timer raises start_stop event.
   * @param {} event: the event with sended data.
   * @memberOf AnalysisTextPage
   */
  public startStopPlaying(event): void {
    if(event.value) {
      // If play is called at the end of the audio, it will reset the audio player.
      if(this.playTimer === this.totalTimer) {
        this.updateTotalTimestamp({timestampTotal: 0});
        this.playTimerComponent.backward();
        this.audioPlayer.currentTime = 0;
      }

      RecordService.play(this.audioPlayer);
      this.inPause = false;
    } else {
      RecordService.stop(this.audioPlayer);
      this.inPause = true;
    }
  }

  /**
   * @name setDrag
   * @desc Set the timeline cursor draggable or not
   * @param {boolean} draggable: set timeline to draggable or not.
   * @memberOf AnalysisTextPage
   */
  public setDrag(draggable: boolean): void {
    this.draggable = draggable;
  }

  /**
   * @name dragPlayCursor
   * @desc On drag on timeline, move play cursor.
   * @param {} event: the event with sended data.
   * @memberOf AnalysisTextPage
   */
  public dragPlayCursor(event): void {
    if(this.draggable) {
      this.moveCursor(event);
    }
  }

  /**
   * @name moveCursor
   * @desc Move the play cursor.
   * @param {} event: the event with sended data.
   * @memberOf AnalysisTextPage
   */
  public moveCursor(event): void {
    if(this.inPause) {
      let totalTemp = this.platform.width() - 33;
      let currentTemp = event.screenX - 16;

      this.playTimer = ((currentTemp  / totalTemp) * this.totalTimer);
      this.audioPlayer.currentTime = this.playTimer / 1000;

      // Avoid moving before the beginning
      if(this.playTimer < 0) {
        this.playTimer = 0;
      }

      // Avoid moving after the end
      if(this.playTimer > this.totalTimer) {
        this.playTimer = this.totalTimer;
      }
    }
  }

  /**
   * @name refreshValues
   * @desc On click on tag, the ref will be sent.
   * @param {} event: the event with sended data.
   * @memberOf AnalysisTextPage
   */
  public refreshValues(event): void {
    // TODO WAITING SPEC
  }

  /**
   * @name forward
   * @desc Add or remove seconds to player.
   * @param {number} time: the duration to increase.
   * @memberOf AnalysisTextPage
   */
  public forward(time): void {
    this.updateTotalTimestamp({timestampTotal: this.playTimer + time * 1000});
    this.audioPlayer.currentTime = this.playTimer / 1000;

    // Set bounds to player
    if(this.playTimer < 0) this.updateTotalTimestamp({timestampTotal: 0});
    if(this.playTimer >= this.totalTimer) this.updateTotalTimestamp({timestampTotal: this.totalTimer});
  }

  /**
   * @name switchTextType
   * @desc Set the display of raw text or synthetic data.
   * @param {boolean} isRawText: the value to set.
   * @memberOf AnalysisTextPage
   */
  public switchTextType(isRawText): void {
    this.isRawText = isRawText;
  }

  /**
   * @name updatePlainText
   * @desc Update raw text when modified
   * @memberOf AnalysisTextPage
   */
  public updatePlainText(): void {
    this.transcriptionsService.update(this.project.id, this.transcriptionId, {textByQuestions: this.currentCutText}).catch((err) => {
      Utils.error(err);
    });
  }

  /**
   * @name getFormatted
   * @desc Return formatted time.
   * @returns {string} the formatted time.
   * @memberOf AnalysisTextPage
   */
  public getFormattedTime(): string {
    return Math.floor(this.minutes) + ":" + Utils.pad2(Math.floor(this.seconds));
  }
}



