import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import * as firebase from "firebase/app";
import { Project } from "../../model/Project";
import { ProjectService } from "../../providers/persistence/project.service";
import { ProjectSinglePage } from "../project-single/project-single";
import { ObjectivesService } from "../../providers/persistence/objectives.service";
import { ViewService } from "../../providers/view/view.service";
import { TranslateService } from "@ngx-translate/core";
import { FormBuilder, FormControl, FormGroup, Validators } from "@angular/forms";
import { ProjectValidator } from "../../validators/project-validator";
import DocumentReference = firebase.firestore.DocumentReference;
import { Objective } from "../../model/Objective";

/**
 * Generated class for the ProjectNewPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@Component({
  selector: 'page-project-new',
  templateUrl: 'project-new.html'
})
export class ProjectNewPage {
  public user: firebase.User;

  private project: Project;
  private pageTitle: string = 'New Project';
  private messageSuccess: string;
  private retryButton: string;
  private messageFail: string;
  private newProjectForm: FormGroup;
  private submitAttempt: boolean = false;

  private nameCtrl: FormControl;

  constructor(public navCtrl: NavController, private projectService: ProjectService, private viewService: ViewService,
              private objectiveService: ObjectivesService, translate: TranslateService,
              private formBuilder: FormBuilder) {
    this.user = firebase.auth().currentUser;
    this.project = new Project();

    translate.get("project.new.message.success").take(1).subscribe(value => this.messageSuccess = value);
    translate.get("project.new.message.fail").take(1).subscribe(value => this.messageFail = value);
    translate.get("project.new.button.retry").take(1).subscribe(value => this.retryButton = value);

    this.nameCtrl = formBuilder.control(
      '',
      Validators.compose([Validators.required, Validators.minLength(3)]),
      control => ProjectValidator.check(this.projectService, control)
    );

    this.newProjectForm = formBuilder.group({
      name: this.nameCtrl,
      description: [''],
      objectives: ['']
    });

  }

  public save(): void {
    this.submitAttempt = true;
    if (this.newProjectForm.valid) {
      this.addProject();
    }
  }

  public addProject(): void {
    let obj = document.getElementById("objectives") as HTMLInputElement;
    this.projectService.create(this.project).then((projectRef: DocumentReference) => {
      this.project.id = projectRef.id;
      let objectives = obj.innerText.trim().split('\n');
      objectives.forEach((o: string, index: number) => {
        this.objectiveService.create(new Objective(o, index), projectRef.id);
      });
      this.viewService.showToast(this.messageSuccess);
      this.navCtrl.push(ProjectSinglePage, {"project": this.project});
    })
      .catch(() => {
        this.viewService.showToast(this.messageFail, 5000, 'bottom', () => this.addProject(), true,
          this.retryButton);
      });
  }

  public reviewChanges($event: any): void {
    let target = $event.target as HTMLInputElement;
    let inner: string = target.innerHTML.trim();

    inner.includes('<ul>');

    if (!inner.includes("<ul>")) {
      target.innerHTML = `<ul><li>${target.innerText}</li></ul>`;
    }
  }
}



