import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Subscription } from "rxjs/Subscription";
import { Observable } from "rxjs/Observable";
import { Project } from "../../model/Project";
import { TranslateService } from "@ngx-translate/core";
import { QuestionService } from "../../providers/persistence/question.service";
import { ObjectivesService } from "../../providers/persistence/objectives.service";
import { AuthProvider } from "../../providers/auth/auth";
import { ProjectService } from "../../providers/persistence/project.service";
import { interviews } from "../../util/constants";
import { RecordService } from "../../providers/recording/record.service";
import { InterviewsService } from "../../providers/persistence/interviews.service";
import { AnalysisPage } from "../analysis/analysis";
import { interviewStatus } from "../../util/enum";
import { TranscriptionsService } from "../../providers/persistence/transcriptions.service";
import { Utils } from "../../util/utils";
import { Objective } from "../../model/Objective";
import { Question } from "../../model/Question";
import { Interview } from "../../model/Interview";
import { Note } from "../../model/Note";

import { ENV } from '@app/env';

/**
 * Controller of interview record page.
 * This page manage the recording of an interview.
 */
@Component({
  selector: 'page-interview-record',
  templateUrl: 'interview-record.html',
})
export class InterviewRecordPage implements OnInit, OnDestroy {

  private subscriptions: Subscription[];

  private pageTitle: string;
  private tab = interviews;

  private selectedQuestion: number;
  private questionTab: Array<Question>;

  private project: Project;
  private interview: Observable<Interview>;
  private questions: Observable<Question[]>;
  private objectives: Observable<Objective[]>;
  private note: Observable<Note[]>;

  private audioPlayer;
  private audioBlob: Blob;
  private channel: Array<Float32Array>;
  private channelSize: number;

  private interviewId: string;
  private interviewRef: Interview;
  private refToAudio: string;
  private timestampTotal: number;
  private inPause: boolean;

  private seconds: number;
  private minutes: number;

  private tags: Array<any>;
  private defaultTags: Array<any>;
  private numberTags: number;

  private limitRequestTries;
  private totalRequestTries;

  private questionTimeTag: string;

  private audioIsUpToDate: boolean;

  /**
   * @name constructor
   * @desc Constructor init our values.
   * @param {NavParams} navParams
   * @param {TranslateService} translate
   * @param {InterviewsService} interviewsService
   * @param {QuestionService} questionService
   * @param {ObjectivesService} objectivesService
   * @param {RecordService} recordService
   * @param {TranscriptionsService} transcriptionsService
   * @param {ProjectService} projectService
   * @param {NavController} navCtrl
   * @param {AuthProvider} authService
   * @memberOf InterviewRecordPage
   */
  constructor(private navParams: NavParams, private translate: TranslateService, private interviewsService: InterviewsService,
              private questionService: QuestionService, private objectivesService: ObjectivesService,
              private recordService: RecordService, private transcriptionsService: TranscriptionsService,
              private projectService: ProjectService, private navCtrl: NavController,
              private authService: AuthProvider) {
    this.project = this.navParams.get("project");
    this.interviewId = this.navParams.get('interviewId');
    this.translate.get('tabs.title.interviews').take(1).subscribe(value => this.pageTitle = value);

    this.selectedQuestion = 0;

    this.timestampTotal = 0;
    this.seconds = 0;
    this.minutes = 0;
    this.inPause = true;

    this.defaultTags = [{
      color: "#000000",
      type: "Commentary",
    }, {
      color: "#b20406",
      type: "Important",
    }, {
      color: "#243cd1",
      type: "Need",
    }, {
      color: "#2f9b42",
      type: "Joker",
    }];

    this.limitRequestTries = 5;
    this.totalRequestTries = 0;

    this.channel = [];
    this.channelSize = 0;

    this.questionTimeTag = "questionTime";

    this.audioIsUpToDate = false;

    this.refToAudio = null;

  }

  /**
   * @name ngOnInit
   * @desc On init, subscribe to observable.
   * @memberOf InterviewRecordPage
   */
  public ngOnInit() {
    // Get data
    this.questions = this.questionService.getOrderedQuestions(this.project.id);
    this.objectives = this.objectivesService.getOrderedObjectives(this.project.id);
    this.interview  = this.interviewsService.getInterviewById(this.project.id, this.interviewId);
    this.note = this.questionService.getNote(this.project.id);

    // Subscription
    this.subscriptions = new Array<Subscription>();

    this.subscriptions.push(this.questions.subscribe((questions) => {
      this.questionTab = questions;
    }));

    this.subscriptions.push(this.interview.subscribe((interview) => {
      this.interviewRef = interview;
      this.tags = interview.tags;
      this.numberTags = interview.tags.filter(((x) => { return x.type !== this.questionTimeTag; })).length;
      this.tags.sort((x1, x2) => {
        return x1.timestamp - x2.timestamp;
      });

      this.refToAudio = interview.refToAudio;

      if(interview.refToAudio && !this.audioIsUpToDate) {


        // Construct the audio blob
        TranscriptionsService.getAudioByRef(interview.refToAudio).then((url) => {
          this.transcriptionsService.getAudioBlobByUrl(url).then((audioBlob) => {
            this.audioBlob = Utils.b64toBlob(audioBlob.data.slice(22), 'audio/wav', 512);
            this.audioPlayer = new Audio(window.URL.createObjectURL(this.audioBlob));

            const fileReaderBuffer = new FileReader();
            this.recordService.promiseFileReaderArrayBuffer(this.audioBlob, fileReaderBuffer).then(() => {
              this.channel = Utils.dataViewToArray(new DataView(fileReaderBuffer.result));
              this.channelSize = this.channel.length * 2048;
            });
          }).catch((err) => Utils.error(err));
        });

        this.timestampTotal = interview.duration * 1000;
        this.seconds = (this.timestampTotal / 1000) % 60;
        this.minutes = (this.timestampTotal / 1000) / 60;
      }

      this.audioIsUpToDate = true;
    }));

    this.subscriptions.push(this.note.subscribe());
  }

  /**
   * @name ngOnDestroy
   * @desc On destroy, unsubscribe to all subscribed observable.
   * @memberOf InterviewRecordPage
   */
  public ngOnDestroy() {
    this.subscriptions.forEach((e) => e.unsubscribe());
  }

  /**
   * @name setFocusQuestion
   * @desc Set the focus to a question (default first).
   * @param {number} index: the index of question to focus.
   * @memberOf InterviewRecordPage
   */
  public setFocusQuestion(index: number): void {
    const oldQuestion = this.selectedQuestion;
    this.selectedQuestion = index;

    if(oldQuestion !== this.selectedQuestion) {
      if(!this.inPause){
        this.addTag("#000000", this.questionTimeTag, this.questionTab[oldQuestion].id);
      }
    }
  }

  /**
   * @name updateTotalTimestamp
   * @desc Update timestamp each time component refresh it.
   * @param {} event: the event with sended data.
   * @memberOf InterviewRecordPage
   */
  public updateTotalTimestamp(event): void {
    this.timestampTotal = event.timestampTotal;
    this.seconds = (event.timestampTotal / 1000) % 60;
    this.minutes = (event.timestampTotal / 1000) / 60;
  }

  /**
   * @name startStopRecording
   * @desc Start/Stop recording when timer raises start_stop event.
   * @param {} event: the event with sended data.
   * @memberOf InterviewRecordPage
   */
  public startStopRecording(event): void {
    if(event.value) {
      // PLAY
      this.inPause = false;
      this.recordService.startRecordingFunction(this.channel, this.channelSize);
    } else {
      // PAUSE
      this.inPause = true;

      let record = this.recordService.stopRecordingFunction();

      this.audioPlayer = record.audio;
      this.audioBlob = record.blob;
      this.channel = record.channel;
      this.channelSize = record.channelSize;

      this.addTag("#000000", this.questionTimeTag, this.questionTab[this.selectedQuestion].id);
    }
  }

  /**
   * @name saveInterview
   * @desc Save the interview into database.
   * @memberOf InterviewRecordPage
   */
  public saveInterview(): void {
     this.navCtrl.push(AnalysisPage, {"project": this.project}).then(() => {
      this.save();
    });
  }

  /**
   * @name save()
   * @desc Send all data to cloud function to persist transcription and audio.
   * @memberOf InterviewRecordPage
   */
  private save(): void {
    // TODO FIX ERROR HttpResponse

    // Remove ref to avoid playing audio during updating in analysis


      const config = {
        enableWordTimeOffsets: true,
        encoding: "WAV",
        sampleRateHertz: 44100,
        languageCode: "fr-FR",
      };

      let audioId = this.refToAudio;
      if(!this.refToAudio){
        audioId = Utils.audioPath(this.project.id);
      }

      // Metadata needed by cloud function
      const metadata = {
        customMetadata: {
          'tags': JSON.stringify(this.tags),
          'interviewId': this.interviewId,
          'projectId': this.project.id,
          'userId': this.authService.getUserId,
          'config': JSON.stringify(config),
          'duration': this.audioPlayer.duration,
          'audioId': audioId
        }
      };

      // Save the audio into storage
      this.transcriptionsService.saveAudioBlob(this.audioBlob, metadata).then((data) => {
        // Change the state of project
        this.projectService.update(this.project.id, {
          preparation: false,
        }).catch((err) => {
          Utils.error(err);
        });
      }).catch((err) => {
        Utils.error(err);
      });
  }


  private request(uri: string){
    this.recordService.httpGetAsync(ENV.urlCloudFunctionSpeech, "fr-FR", this.tags, this.interviewId, this.project.id, this.authService.getUserId, uri).then(() => {

      this.totalRequestTries = 0;
    }).catch((err) => {
      Utils.error(err);
      if(err.status !== 200 && this.totalRequestTries < this.limitRequestTries) {
        this.request(uri);
        this.totalRequestTries++;
      }
    });
  }

  /**
   * @name addTag
   * @desc Add a tag and save it.
   * @param {string} color: the color.
   * @param {string} type: the type.
   * @param {string} refToQuestion: the ref to the question.
   * @memberOf InterviewRecordPage
   */
  public addTag(color: string, type: string, refToQuestion: string): void {
    const tagToAdd = {
      color: color,
      type: type,
      refToQuestion: refToQuestion,
      timestamp: this.timestampTotal,
      id: this.tags.length,
    };

    this.tags.push(tagToAdd);

    this.interviewsService.update(this.project.id, this.interviewId, {
      tags: this.tags,
    }).catch((err) => {
      Utils.error(err);
    });
  }

  /**
   * @name updateCommentary
   * @desc Update commentary in database.
   * @memberOf InterviewRecordPage
   */
  public updateCommentary(): void {
    this.interviewsService.update(this.project.id, this.interviewId, {
      tags: this.tags,
    }).catch((err) => {
      Utils.error(err);
    });
  }

  /**
   * @name deleteTag
   * @desc Delete a tag.
   * @param {string} id: the id of the tag.
   * @memberOf InterviewRecordPage
   */
  public deleteTag(id: string): void {
    this.tags = this.tags.filter((tag) => { return tag.id !== id; });
    this.interviewsService.update(this.project.id, this.interviewId, {
      tags: this.tags,
    }).catch((err) => {
      Utils.error(err);
    });
  }

  /**
   * @name getFormatted
   * @desc Return formatted timer's time.
   * @returns {string} the formatted time.
   * @memberOf InterviewRecordPage
   */
  public getFormattedTime(): string {
    return Math.floor(this.minutes) + ":" + Utils.pad2(Math.floor(this.seconds));
  }

  /**
   * @name getFormattedTimeTag
   * @desc Return formatted time.
   * @returns {string} the formatted time.
   * @memberOf InterviewRecordPage
   */
  public getFormattedTimeTag(time: number): string {
    return Math.floor((time / 1000) / 60) + ":" + Utils.pad2(Math.floor((time / 1000) % 60));
  }
}



