/**
 * State
 * Different states of timer.
 * To manage buttons.
 */
export class State {

  private _play: boolean = true;
  private _stop: boolean = false;
  private _backward: boolean = false;

  /**
   * @name stop
   * @desc Return true if state is stop. False else.
   * @returns {boolean}
   * @memberOf State
   */
  public get stop(): boolean { return this._stop; }

  /**
   * @name backward
   * @desc Return true if state is backward. False else.
   * @returns {boolean}
   * @memberOf State
   */
  public get backward(): boolean { return this._backward; }

  /**
   * @name play
   * @desc Return true if state is stop. False else.
   * @returns {boolean}
   * @memberOf State
   */
  public get play(): boolean { return this._play; }

  /**
   * @name setPlay
   * @desc Set to the play state.
   * @returns {boolean}
   * @memberOf State
   */
  public setPlay() {
    this._stop = true;
    this._play = this._backward = false;
  }

  /**
   * @name setStop
   * @desc Set to the stop state.
   * @returns {boolean}
   * @memberOf State
   */
  public setStop() {
    this._stop = false;
    this._play = this._backward = true;
  }

  /**
   * @name setBackward
   * @desc Set to the backward state.
   * @returns {boolean}
   * @memberOf State
   */
  public setBackward() {
    this._stop = true;
    this._play = this._backward = false;
  }
}



