import { Component } from '@angular/core';
import { AuthProvider } from "../../providers/auth/auth";
import { LoadingController, NavController } from "ionic-angular";
import { Loading } from "ionic-angular/components/loading/loading";
import { MyMainApp } from "../../main-app/main-app.component";

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  spinner: Loading;

  constructor(public authProvider: AuthProvider, private navCtrl: NavController, private loadingCtrl: LoadingController) {
    this.spinner = this.loadingCtrl.create({
      spinner: "crescent",
      content: 'Please wait...',
      duration: 10000,
      dismissOnPageChange: true
    });
  }

  async ionViewWillEnter() {
    this.spinner.present();
    try {
      console.log("try to retrieve login");
      await this.authProvider.retrieveLogin();
      this.spinner.dismiss();
    } catch (e) {
      console.log(e);
    }
  }

  ionViewCanLeave(){
    return this.authProvider.isLoggedIn();
  }


  login() {
    try {
      this.authProvider.loginWithGoogle();
    }
    catch (e) {
      console.log(e);
    }
  }

  isLoggedIn(): boolean{
    return this.authProvider.isLoggedIn();
  }


  goToHome() {
    this.navCtrl.push(MyMainApp);
    // this.navCtrl.setRoot('MyMainApp');
  }
}



