import { AngularFirestore } from "angularfire2/firestore";
import { Interview } from "../../model/Interview";
import { Observable } from "rxjs/Observable";
import * as firebase from "firebase/app";
import "rxjs/add/operator/first";
import DocumentReference = firebase.firestore.DocumentReference;
import { Injectable } from "@angular/core";
import { pathInterviews, replaceId } from "../../util/constants";

@Injectable()
export class InterviewsService {

  constructor(private firestore: AngularFirestore) {
  }

  public create(interview: Interview, idProject: string): Promise<DocumentReference> {
    return this.firestore.collection<Interview>(InterviewsService.getPath(idProject)).add({...interview});
  }

  public getInterviews(idProject: string): Observable<Interview[]> {
    return this.firestore.collection<Interview>(InterviewsService.getPath(idProject))
      .snapshotChanges().map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data() as Interview;
          const id = a.payload.doc.id;
          return {id, ...data};
        });
      });
  }

  public getInterviewById(idProject: string, idInterview: string): Observable<Interview> {
    return this.firestore.doc<Interview>(`${InterviewsService.getPath(idProject)}/${idInterview}`).valueChanges();
  }

  public update(idProject: string, idInterview: string, data: Partial<Interview>): Promise<void> {
    return this.firestore.doc<Interview>(`${InterviewsService.getPath(idProject)}/${idInterview}`).update(data);
  }

  public remove(idProject: string, idInterview: string): Promise<void> {
    return this.firestore.doc<Interview>(`${InterviewsService.getPath(idProject)}/${idInterview}`).delete();
  }

  private static getPath(idProj: string): string {
    return pathInterviews.replace(replaceId, idProj);
  }
}



