import { Component, OnDestroy, OnInit } from "@angular/core";
import { NavParams } from "ionic-angular";
import { Project} from "../../model/Project";
import { preparation, questionCreator } from "../../util/constants";
import { TranslateService } from "@ngx-translate/core";
import "rxjs/add/operator/take";
import { QuestionService } from "../../providers/persistence/question.service";
import { ReorderIndexes } from "ionic-angular/components/item/item-reorder";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { Subscription } from "rxjs/Subscription";
import { ViewService } from "../../providers/view/view.service";
import { Observable } from "rxjs/Observable";
import { Question } from "../../model/Question";
import { Note } from "../../model/Note";

@Component({
  selector: 'interview-flow',
  templateUrl: 'interview-flow.html'
})
export class InterviewFlowPage implements OnInit, OnDestroy {
  private pageTitle: string;
  private pageSubtitle: string;
  private project: Project;
  private tab = preparation;
  private subTab = questionCreator;
  private edit: boolean = false;
  private questions: BehaviorSubject<Question[]> = new BehaviorSubject<Question[]>([]);
  private notes: Note = new Note();

  private subs: Subscription[] = [];

  private messageReorderSuccess: string;
  private messageReorderFail: string;
  private messageNoteSuccess: string;
  private messageNoteFail: string;
  private buttonReorder: string;
  private buttonDoneReorder: string;

  private addingNotes: boolean;
  private myNotes: Observable<Note>;
  private savingOrder: boolean = false;

  constructor(private navParams: NavParams, private translate: TranslateService,
              private  questionService: QuestionService, private viewService: ViewService) {
    this.project = this.navParams.get("project");
    this.translate.get('tabs.title.preparation').take(1).subscribe(value => this.pageTitle = value);
    this.translate.get('tabs.title.questionCreator').take(1).subscribe(value => this.pageSubtitle = value);
    this.translate.get('interviewFlow.toast.success.reorder').take(1)
      .subscribe(value => this.messageReorderSuccess = value);
    this.translate.get('interviewFlow.toast.error.reorder').take(1).subscribe(value => this.messageReorderFail = value);
    this.translate.get('interviewFlow.toast.success.notes').take(1).subscribe(value => this.messageNoteSuccess = value);
    this.translate.get('interviewFlow.toast.error.notes').take(1).subscribe(value => this.messageNoteFail = value);
    this.translate.get('interviewFlow.button.reorder').take(1).subscribe(value => this.buttonReorder = value);
    this.translate.get('interviewFlow.button.done').take(1).subscribe(value => this.buttonDoneReorder = value);
    this.myNotes =
      this.questionService.getNote(this.project.id).map((value: Note[]) => value.length > 0 ? value[0] : new Note()).share();
  }

  get buttonReorderText() {
    return this.edit ? this.buttonDoneReorder : this.buttonReorder;
  }

  ngOnInit() {
    this.subs.push(
      this.questionService.getOrderedQuestions(this.project.id).subscribe(value => {
        this.questions.next(value);
      }),

      this.myNotes.subscribe(value => {
        this.notes = value
      })
    );
  }

  ngOnDestroy() {
    this.subs.forEach(s => s.unsubscribe());
  }

  reorderItems($event: ReorderIndexes) {
    let questionsLocal = this.questions.getValue();

    this.reorder(questionsLocal, $event.from, $event.to);
    this.questions.next(questionsLocal);
  }

  reorder(array: { order: number }[], fromIndex, toIndex) {
    let elementFrom = array[fromIndex];

    array.splice(fromIndex, 1);
    array.splice(toIndex, 0, elementFrom);

    array.forEach((elt, index) => elt.order = index);
  }

  addNotes() {
    this.addingNotes = true;
  }

  toggleEdit() {
    if (this.edit) {
      this.savingOrder = true;
      // save everything
      this.questionService.updateOrderBasedOnIndex(this.project.id, this.questions.getValue()).then(value => {
        this.edit = false;
        this.savingOrder = false;
        this.viewService.showToast(this.messageReorderSuccess);
      }).catch(reason => {
        this.viewService.showToast(this.messageReorderFail + ", reason: " + reason, 5000);
        this.savingOrder = false;
      });
    } else {
      this.edit = true;
    }
  }

  saveNote() {
    // save (or update ?)
    if (this.notes.id) {
      this.questionService.updateNote(this.notes.note, this.project.id, this.notes.id).then(value => {
        this.viewService.showToast(this.messageNoteSuccess);
        this.addingNotes = false;
      }).catch(reason => {
        this.viewService.showToast(this.messageNoteFail, 5000);
      });
    } else {
      this.questionService.createNote(this.notes.note, this.project.id).then(value => {
        this.viewService.showToast(this.messageNoteSuccess);
        this.addingNotes = false;
      }).catch(reason => {
        this.viewService.showToast(this.messageNoteFail, 5000);
      });
    }
  }
}



