import { AngularFirestore, DocumentChangeAction } from "angularfire2/firestore";
import { Note} from "../../model/Note";
import { Observable } from "rxjs/Observable";
import * as firebase from "firebase/app";
import "rxjs/add/operator/first";
import { Injectable } from "@angular/core";
import { pathQuestions, replaceId } from "../../util/constants";

import uuid from "uuid-random";
import DocumentReference = firebase.firestore.DocumentReference;
import DocumentSnapshot = firebase.firestore.DocumentSnapshot;
import Transaction = firebase.firestore.Transaction;
import { Objective } from "../../model/Objective";
import { Question } from "../../model/Question";

@Injectable()
export class QuestionService {

  constructor(private firestore: AngularFirestore) {
  }

  public create(question: Question, idProject: string): Promise<DocumentReference> {
    return this.firestore.collection<Question>(QuestionService.getPath(idProject)).add({...question});
  }

  /**
   * @desc Create all questions linked to the objectives in a transaction
   * @param {string[]} questionsTexts the questions in the form of blocks of texts (questionsTexts[0] corresponds
   * to objectives[0], with N questions (separated by '\n' character).
   * @param {Objective[]} objectives the related objectives
   * @param {string} idProject id of the project
   * @returns {Promise<[]>} succeeds if all questions were saved, else fails
   */
  public createQuestions(questionsTexts: string[], objectives: Objective[], idProject: string): Promise<any[]> {
    return firebase.firestore().runTransaction((transaction: Transaction) => {
      let createPromises = [];

      questionsTexts.forEach((questionBlocks, index) => {
        let questions = questionBlocks.trim().split('\n');

        questions.forEach(question => {
          createPromises.push(
            this.transactionSetQuestion(transaction, QuestionService.newRandomIdQuestion(idProject),
              new Question(question, Number.MAX_SAFE_INTEGER, objectives[index].id, new Date()))
          );
        });
      });

      return Promise.all(createPromises);
    });
  }

  /**
   * Creates a question linked to an objective
   *
   * @param {string} questionText
   * @param {string} objectiveId
   * @param {string} idProject
   * @return {Promise<DocumentReference>}
   */
  public createOneQuestion(questionText: string, objectiveId: string, idProject: string): Promise<DocumentReference> {
    return this.create(new Question(questionText, Number.MAX_SAFE_INTEGER, objectiveId, new Date()), idProject);
  }

  public updateOrderBasedOnIndex(projectId: string, questions: Question[]): Promise<any[]> {
    let projectPath = QuestionService.getPath(projectId);

    return firebase.firestore().runTransaction(transaction => {
      let updatePromises = [];
      let i: number;
      for (i = 0; i < questions.length; i++) {
        let refQuestion: DocumentReference = QuestionService.getQuestionReferenceFromPath(projectPath, questions[i].id);
        updatePromises.push(
          this.transactionUpdateQuestion(transaction, refQuestion, {order: i})
        );
      }

      return Promise.all(updatePromises);
    })
  }

  public transactionUpdateQuestion(transaction: Transaction, idQuestion: DocumentReference, question: Partial<Question>)
  : Promise<any> {
    return transaction.get(idQuestion).then((snapshot: DocumentSnapshot) => {
      transaction.update(snapshot.ref, {...question});
    });
  }

  public getQuestions(idProject: string): Observable<Question[]> {
    return this.firestore.collection<Question>(QuestionService.getPath(idProject))
      .snapshotChanges().map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data() as Question;
          const id = a.payload.doc.id;
          return {id, ...data};
        });
      });
  }

  public existForObjective(idObjective: string, idProj: string): Observable<boolean> {
    return this.firestore.collection<Question>(QuestionService.getPath(idProj), ref => ref.where('objectifId', '==', idObjective))
      .valueChanges(['added', 'removed']).map(q => q.length > 0);
  }

  public removeAllFromObjective(idObjective: string, idProject: string): Promise<any> {
    return new Promise((resolve, reject) => {
      this.firestore.collection<Question>(QuestionService.getPath(idProject),
        ref => ref.where('objectifId', '==', idObjective))
        .snapshotChanges().take(1).subscribe((snapshot: DocumentChangeAction[]) => {
        let batch = firebase.firestore().batch();
        snapshot.forEach(doc => batch.delete(doc.payload.doc.ref));
        batch.commit().then(() => resolve(), (error) => reject(error));
      });
    });
  }

  public getOrderedQuestions(idProject: string): Observable<Question[]> {
    return this.firestore.collection<Question>(QuestionService.getPath(idProject),
      ref => ref.orderBy('order').orderBy('dateCreation'))
      .snapshotChanges().map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data() as Question;
          const id = a.payload.doc.id;
          return {id, ...data};
        });
      });
  }

  public getQuestionById(idProject: string, idQuestion: string): Observable<Question> {
    return this.firestore.doc<Question>(`${QuestionService.getPath(idProject)}/${idQuestion}`).valueChanges();
  }

  public findQuestion(idProject: string, name: string): Observable<Question[]> {
    return this.firestore.collection(QuestionService.getPath(idProject), ref => ref.where('name', '==', name)).snapshotChanges()
      .map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data() as Question;
          const id = a.payload.doc.id;
          return {id, ...data};
        });
      });
  }

  public update(id: string, idProject: string, data: Partial<Question>): Promise<void> {
    return this.firestore.doc<Question>(`${QuestionService.getPath(idProject)}/${id}`).update(data);
  }

  public remove(id: string, idProject: string): Promise<void> {
    return this.firestore.doc<Question>(`${QuestionService.getPath(idProject)}/${id}`).delete();
  }

  public createNote(note: string, idProject: string): Promise<DocumentReference> {
    return this.firestore.collection<Note>(QuestionService.getPath(idProject)).add({...new Note(note)});
  }

  public updateNote(note: string, idProject: string, idNote: string) {
    return this.firestore.collection<Note>(QuestionService.getPath(idProject)).doc(idNote).update({note: note});
  }

  public getNote(idProject: string): Observable<Note[]> {
    return this.firestore.collection<Note>(QuestionService.getPath(idProject), ref => ref.where('isNote', '==', true).limit(1))
      .snapshotChanges().map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data() as Note;
          const id = a.payload.doc.id;
          return {id, ...data};
        });
      });
  }

  private transactionSetQuestion(transaction: Transaction, randomIdQuestion: DocumentReference, question: Question)
  : Promise<any> {
    return transaction.get(randomIdQuestion).then((snapshot: DocumentSnapshot) => {
      transaction.set(snapshot.ref, {...question});
    });
  }

  private static getQuestionReferenceFromPath(pathToProject: string, idQuestion: string): DocumentReference {
    return firebase.firestore().collection(pathToProject).doc(idQuestion);
  }

  private static newRandomIdQuestion(idProject: string): DocumentReference {
    return firebase.firestore().collection(`${QuestionService.getPath(idProject)}`).doc(uuid());
  }

  private static getPath(idProj: string): string {
    return pathQuestions.replace(replaceId, idProj);
  }
}



