import { Component, Input, OnInit } from '@angular/core';
import { NavController, ViewController } from "ionic-angular";
import { Project } from "../../model/Project";
import { QuestionCreatorPage } from "../../pages/question-creator/question-creator";
import { interviewTagPrepa, participantsPanel, questionCreator } from "../../util/constants";
import { InterviewTagPage } from "../../pages/interview-tag/interview-tag";
import { ParticipantPanelPage } from "../../pages/participant-panel/participant-panel";
import { TranslateService } from "@ngx-translate/core";


@Component({
  selector: 'preparation-segments',
  templateUrl: 'preparation-segments.html'
})
export class PreparationSegmentsComponent implements OnInit {

  @Input() pageTitle: string;
  @Input() project: Project;
  @Input() tab: { name: string, value: number };

  private tabQuestionCreator = QuestionCreatorPage;
  private tabInterviewTagPreparation = InterviewTagPage;
  private tabParticipantsPanel = ParticipantPanelPage;
  private tabName: string;

  private questionCreator = questionCreator;
  private interviewTagPrepa = interviewTagPrepa;
  private participantsPanel = participantsPanel;

  private tabNameQuestionCreator: string;
  private tabNameInterviewTagPrepa: string;
  private tabNameParticipantsPanel: string;

  constructor(private navCtrl: NavController, private viewCtrl: ViewController,
              private translate: TranslateService) {
    this.translate.get('tabs.title.questionCreator').take(1).subscribe(value => this.tabNameQuestionCreator = value);
    this.translate.get('tabs.title.interviewTag').take(1).subscribe(value => this.tabNameInterviewTagPrepa = value);
    this.translate.get('tabs.title.participantsPanel').take(1)
      .subscribe(value => this.tabNameParticipantsPanel = value);
  }

  public ngOnInit() {
    this.tabName = this.tab.name;
  }

  public selectTab(tab: { name: string, value: number }) {
    let forward = (tab.value - this.tab.value) > 0;
    switch (tab) {
      case questionCreator:
        this.replacePageWith(this.tabQuestionCreator, {"project": this.project}, forward, tab);
        break;
      case interviewTagPrepa:
        this.replacePageWith(this.tabInterviewTagPreparation, {"project": this.project}, forward, tab);
        break;
      case participantsPanel:
        this.replacePageWith(this.tabParticipantsPanel, {"project": this.project}, forward, tab);
        break;
    }
  }

  public replacePageWith(page, params, forward, tab: { name: string, value: number }) {
    this.tab = tab;
    this.navCtrl.push(page, params,
      {animate: true, direction: forward ? 'forward' : 'back', animation: 'transition'})
      .then(() => {
        // first we find the index of the current view controller
        const index = this.viewCtrl.index;

        // then we remove it from the navigation stack
        this.navCtrl.remove(index);
      });
  }
}



