import { Component, Input, OnInit } from '@angular/core';
import { ProjectSinglePage } from "../../pages/project-single/project-single";
import { NavController, ViewController } from "ionic-angular";
import { LoginPage } from "../../pages/login/login";
import { AuthProvider } from "../../providers/auth/auth";
import { Project } from "../../model/Project";
import { QuestionCreatorPage } from "../../pages/question-creator/question-creator";
import { analysis, interviews, objectives, preparation } from "../../util/constants";
import { InterviewPage } from "../../pages/interview/interview";
import { AnalysisPage } from "../../pages/analysis/analysis";


@Component({
  selector: 'custom-header',
  templateUrl: 'custom-header.html'
})
export class CustomHeaderComponent implements OnInit {

  @Input() pageTitle: string;
  @Input() project: Project;
  @Input() tab: { name: string, value: number };

  private tabObjectives = ProjectSinglePage;
  private tabPreparation = QuestionCreatorPage;
  private tabInterviews = InterviewPage;
  private tabAnalysis = AnalysisPage;
  private tabName: string;

  private objectives = objectives;
  private preparation = preparation;
  private interviews = interviews;
  private analysis = analysis;

  constructor(private authProvider: AuthProvider, private navCtrl: NavController, private viewCtrl: ViewController) {
  }

  public  ngOnInit() {
    this.tabName = this.tab.name;
  }

  public selectTab(tab: { name: string, value: number }) {
    let forward = (tab.value - this.tab.value) > 0;
    switch (tab) {
      case objectives:
        this.replacePageWith(this.tabObjectives, {"project": this.project}, "Objectifs", forward, tab);
        break;
      case preparation:
        this.replacePageWith(this.tabPreparation, {"project": this.project}, "Preparation", forward, tab);
        break;
      case interviews:
        this.replacePageWith(this.tabInterviews, {"project": this.project}, "Interviews", forward, tab);
        break;
      case analysis:
        this.replacePageWith(this.tabAnalysis, {"project": this.project}, "Analysis", forward, tab);
        break;
    }
  }

  public replacePageWith(page, params, title, forward, tab: { name: string, value: number }) {
    this.pageTitle = title;
    this.tab = tab;

    this.navCtrl.push(page, params,
      {animate: true, direction: forward ? 'forward' : 'back', animation: 'transition'})
      .then(() => {
        // first we find the index of the current view controller
        const index = this.viewCtrl.index;

        // then we remove it from the navigation stack
        this.navCtrl.remove(index);
      });
  }

  public goToHome() {
    this.pageTitle = "Projects";
    this.navCtrl.popToRoot();
  }

  public logout() {
    try {
      this.authProvider.logout().then(() => {
        this.navCtrl.setRoot(LoginPage);
      })
    }
    catch (e) {
      console.log(e);
    }
  }
}



