import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ENV } from '@app/env';

/**
 * RecordComponent
 * Class to manage interaction between app and server.
 * Can record audio and play it.
 */
@Injectable()
export class RecordService {

  private channel: Array<Float32Array>;
  private channelSize: number;

  private recorder = null;
  private mediaStream = null;
  private sampleRate = 44100;
  private context = null;
  private resultText = null;
  private transcriptionCollection;

  public loading = false;

  /**
   * @name getResultText
   * @desc Return the result text sent by server.
   * @returns {String}
   * @memberOf RecordService
   */
  public get getResultText(): String { return this.resultText; }

  /**
   * @name isLoading
   * @desc Return the actual loading state of process.
   * @returns {boolean}
   * @memberOf RecordService
   */
  public get isLoading(): boolean { return this.loading; }

  /**
   * @name setFlagsCollection
   * @desc Set the tag collection.
   * @param {Array} tagsCollection: the list of tags.
   * @memberOf RecordService
   */
  public setFlagsCollection(tagsCollection: Array<any>): void { this.transcriptionCollection = tagsCollection; }

  /**
   * Constructor needs http object to send request.
   * @param {HttpClient} http
   */
  constructor(private http: HttpClient) {}

  /**
   * @name startRecordingFunction
   * @desc Start the recording of the voice.
   * @param {Array<Float32Array>} channel: the stored record.
   * @param {number} channelSize: the stored size of record.
   * @memberOf RecordService
   */
  public startRecordingFunction(channel: Array<Float32Array>, channelSize: number): void {
    this.channel = channel;
    this.channelSize = channelSize;

    // Initialize recorder
    navigator.getUserMedia(
      {
        audio: true
      }, (e) => {
        // creates the audio context
        this.context = new AudioContext();

        // creates an audio node from the microphone incoming stream
        this.mediaStream = this.context.createMediaStreamSource(e);

        // https://developer.mozilla.org/en-US/docs/Web/API/AudioContext/createScriptProcessor
        // bufferSize: the onaudioprocess event is called when the buffer is full
        let bufferSize = 2048;
        let numberOfInputChannels = 1;
        let numberOfOutputChannels = 1;

        if (this.context.createScriptProcessor) {
          this.recorder = this.context.createScriptProcessor(bufferSize, numberOfInputChannels, numberOfOutputChannels);
        } else {
          this.recorder = this.context.createJavaScriptNode(bufferSize, numberOfInputChannels, numberOfOutputChannels);
        }

        this.recorder.onaudioprocess = (e) => {
          this.channel.push(new Float32Array(e.inputBuffer.getChannelData(0)));
          this.channelSize += bufferSize;
        };

        this.mediaStream.connect(this.recorder);
        this.recorder.connect(this.context.destination);
      },
      function (e) {
        console.error(e);
      });
  }

  /**
   * @name stopRecordingFunction
   * @desc Pause the recording of the voice.
   * @return {} all data needed to persist audio.
   * @memberOf RecordService
   */
  public stopRecordingFunction(): any {
    // Stop recording
    this.recorder.disconnect(this.context.destination);
    this.mediaStream.disconnect(this.recorder);

    let leftBuffer = RecordService.flattenArray(this.channel, this.channelSize);
    let buffer = new ArrayBuffer(44 + leftBuffer.length * 2);
    let view = new DataView(buffer);

    // RIFF chunk descriptor
    RecordService.writeUTFBytes(view, 0, 'RIFF');
    // view.setUint32(4, 44 + interleaved.length * 2, true);
    view.setUint32(4, 44 + leftBuffer.length * 2, true);
    RecordService.writeUTFBytes(view, 8, 'WAVE');
    // FMT sub-chunk
    RecordService.writeUTFBytes(view, 12, 'fmt ');

    view.setUint32(16, 16, true); // chunkSize
    view.setUint16(20, 1, true); // wFormatTag
    view.setUint16(22, 1, true); // wChannels: stereo (2 channels), mono (1)
    view.setUint32(24, this.sampleRate, true); // dwSamplesPerSec
    view.setUint32(28, this.sampleRate * 2, true); // dwAvgBytesPerSec sampleRate * nbChans * 2
    view.setUint16(32, 2, true); // wBlockAlign nbChans * 2
    view.setUint16(34, 16, true); // wBitsPerSample

    // Data sub-chunk
    RecordService.writeUTFBytes(view, 36, 'data');

    // view.setUint32(40, interleaved.length * 2, true);
    view.setUint32(40, leftBuffer.length * 2, true);

    // Write the PCM samples
    let index = 44;
    let volume = 1;

    for (let i = 0; i < leftBuffer.length; i++) {
      view.setInt16(index, leftBuffer[i] * (0x7FFF * volume), true);
      index += 2;
    }

    // Our final blob
    let blob = new Blob([view], {type: 'audio/wav'});

    // Create instance of audio source
    let url = window.URL.createObjectURL(blob);
    let audio = new Audio(url);

    return {audio, blob, channel: this.channel, channelSize: this.channelSize};
  }

  /**
   * @name play
   * @desc Play the recorded voice.
   * @param {Audio} audio: the audio to play.
   * @memberOf RecordService
   */
  public static play(audio) {
    audio.play();
  }

  /**
   * @name stop
   * @desc Pause the recorded voice.
   * @param {Audio} audio: the audio to pause.
   * @memberOf RecordService
   */
  public static stop(audio) {
    audio.pause();
  }

  /**
   * @name reset
   * @desc Reset the recorded voice.
   * @param {Audio} audio: the audio to reset.
   * @memberOf RecordService
   */
  public static reset(audio) {
    audio.pause();
    audio.currentTime = 0;
  }

  /**
   * @name flattenArray
   * @desc Flat arrays.
   * @param {Array<Float32Array>} channelBuffer: the buffer to flat.
   * @param {number} recordingLength: the length of the array.
   * @returns {Float32Array} the flatten array.
   * @memberOf RecordService
   */
  public static flattenArray(channelBuffer, recordingLength) {
    let result = new Float32Array(recordingLength);
    let offset = 0;

    for (let i = 0; i < channelBuffer.length; i++) {
      let buffer = channelBuffer[i];
      result.set(buffer, offset);
      offset += buffer.length;
    }

    return result;
  }

  /**
   * @name writeUTFBytes
   * @desc Fill a tab with UTF Bytes.
   * @param {DataView} view: the whole data.
   * @param {number} offset: the offset for writing.
   * @param {string} string: the string to write.
   * @memberOf RecordService
   */
  public static writeUTFBytes(view: DataView, offset: number, string: string) {
    for (let i = 0; i < string.length; i++) {
      view.setUint8(offset + i, string.charCodeAt(i));
    }
  }

  /**
   * @name httpGetAsync
   * @desc Send a request to process voice.
   * @param {string} theUrl: url to request.
   * @param {string} countryKey: the country key.
   * @param {Array} tagArray: the array of tags.
   * @param {string} interviewId: the id of the interview.
   * @param {string} projectId: the if of project.
   * @param {string} gcsUri: the Google Cloud Storage Uri to send.
   * @param {string} userId: the id of user.
   * @returns {Promise}
   * @memberOf RecordService
   */
  public httpGetAsync(theUrl: string, countryKey: string, tagArray: Array<any>, interviewId: string, projectId: string, userId: string, gcsUri: string): Promise<any> {
    let promise = Promise.resolve();
    return promise.then(() => {

      const fullUri = `${ENV.urlStorage}/${gcsUri}`;

      const audio = {
        uri: fullUri,
      };

      const config = {
        enableWordTimeOffsets: true,
        encoding: "WAV",
        sampleRateHertz: 44100,
        languageCode: countryKey,
      };

      const myBody = {
        audio: audio,
        config: config,
      };

      const fullBody = {
        myBody: myBody,
        tags: tagArray,
        interviewId: interviewId,
        projectId: projectId,
        userId: userId,
      };

      const httpOptions = {
        headers: new HttpHeaders({
          'Content-Type':  'application/json',
          'Access-Control-Allow-Origin': '*',
          'responseType': 'text'
        })
      };

      return this.http.post(theUrl, fullBody, httpOptions).toPromise();
    });
  }

  /**
   * @name promiseFileReader
   * @desc Transform FileReader onloadend event into Promise.
   * @param {Blob} file: file to read.
   * @param {FileReader} fileReader: file reader.
   * @returns {Promise}
   * @memberOf RecordService
   */
  public promiseFileReaderURL(file: Blob, fileReader: FileReader){
    return new Promise((resolve, reject) => {
      fileReader.onloadend = resolve;
      fileReader.readAsDataURL(file);
    });
  }

  /**
   * @name promiseFileReaderArrayBuffer
   * @desc Transform FileReader onloadend event into Promise.
   * @param {Blob} file: file to read.
   * @param {FileReader} fileReader: file reader.
   * @returns {Promise}
   * @memberOf RecordService
   */
  public promiseFileReaderArrayBuffer(file: Blob, fileReader: FileReader){
    return new Promise((resolve) => {
      fileReader.onloadend = resolve;
      fileReader.readAsArrayBuffer(file);
    });
  }
}



