import { Component, OnDestroy, OnInit } from "@angular/core";
import { Project } from "../../model/Project";
import { Observable } from "rxjs/Observable";
import { NavController } from "ionic-angular";
import { ProjectSinglePage } from "../project-single/project-single";
import { ProjectNewPage } from "../project-new/project-new";
import { ProjectService } from "../../providers/persistence/project.service";
import { Subscription } from "rxjs/Subscription";
import * as firebase from "firebase/app";
import "rxjs/add/operator/concatMap";
import "rxjs/add/operator/mergeMap"
import "rxjs/add/observable/merge";
import "rxjs/add/operator/every";

@Component({
  selector: 'page-projects',
  templateUrl: 'projects.html'
})
export class ProjectsPage implements OnInit, OnDestroy {

  public projects: Observable<Project[]>;
  private subs: Subscription[];
  public user: firebase.User;

  private pageTitle: string = "All projects";
  private showProgress: boolean;

  constructor(private navCtrl: NavController, private projectService: ProjectService) {
    this.user = firebase.auth().currentUser;
    this.subs = [];
    this.projects = this.projectService.getProjects().share();

    this.showProgress = true;
  }

  public ngOnInit(): void {
    this.subs.push(this.projects.subscribe(() => this.showProgress = false));
  }

  public ngOnDestroy(): void {
    this.subs.forEach(sub => {
      sub.unsubscribe();
    });
  }

  public gotoProject(project: Project): void {
    this.navCtrl.push(ProjectSinglePage, {"project": project});
  }

  public newProject(): void {
    this.navCtrl.push(ProjectNewPage);
  }

  public ionViewWillLeave() {
    this.subs.forEach(sub => {
      sub.unsubscribe();
    });
  }

  public ionViewWillEnter() {
    this.subs.push(this.projects.subscribe(() => {
      this.showProgress = false;
    }));
  }


}



