import { Component, OnDestroy, OnInit } from "@angular/core";
import { NavParams } from "ionic-angular";
import { Project } from "../../model/Project";
import { interviewTagPrepa, preparation } from "../../util/constants";
import { TranslateService } from "@ngx-translate/core";
import "rxjs/add/operator/take";

@Component({
  selector: 'interview-tag',
  templateUrl: 'interview-tag.html'
})
export class InterviewTagPage implements OnInit, OnDestroy {
  pageTitle: string;
  pageSubtitle: string;
  project: Project;
  tab = preparation;
  subTab = interviewTagPrepa;

  constructor(private navParams: NavParams, private translate: TranslateService) {
    this.project = this.navParams.get("project");
    this.translate.get('tabs.title.preparation').take(1).subscribe(value => this.pageTitle = value);
    this.translate.get('tabs.title.interviewTag').take(1).subscribe(value => this.pageSubtitle = value);
  }

  ngOnInit() {

  }

  ngOnDestroy() {

  }

}



