import { AngularFirestore, DocumentChangeAction } from "angularfire2/firestore";
import { Injectable } from "@angular/core";
import { Participant } from "../../model/Participant";
import { pathParticipants, replaceId } from "../../util/constants";
import * as firebase from "firebase/app";
import { Observable } from "rxjs/Observable";
import { Utils } from "../../util/utils";
import { ImageService } from "./image.service";
import DocumentReference = firebase.firestore.DocumentReference;

@Injectable()
export class ParticipantService {
  constructor(private firestore: AngularFirestore, private imageService: ImageService) {
  }

  create(participant: Participant, idProject: string): Promise<DocumentReference> {
    return this.firestore.collection<Participant>(ParticipantService.getPath(idProject))
      .add({...Utils.clean(participant)});
  }

  createWithId(participant: Participant, idProject: string) {
    const {id, ...p} = participant;
    return this.firestore.doc<Participant>(`${ParticipantService.getPath(idProject)}/${id}`)
      .set({...Utils.clean(p)});
  }

  getParticipants(idProject: string): Observable<Participant[]> {
    return this.firestore.collection<Participant>(ParticipantService.getPath(idProject))
      .snapshotChanges().map((actions: DocumentChangeAction[]) => {
        return actions.map((a: DocumentChangeAction) => {
          const participant = a.payload.doc.data() as Participant;
          const id = a.payload.doc.id;
          return {id, ...participant};
        });
      });
  }

  update(id: string, data: Partial<Participant>, idProject: string): Promise<void> {
    return this.firestore.doc(`${ParticipantService.getPath(idProject)}/${id}`).update(data);
  }

  remove(id: string, idProject: string): Promise<void> {
    this.imageService.deleteImage(idProject, id);
    return this.firestore.doc<Participant>(`${ParticipantService.getPath(idProject)}/${id}`).delete();
  }

  private static getPath(idProject: string): string {
    return pathParticipants.replace(replaceId, idProject);
  }
}



