import {NgModule} from '@angular/core';
import {IonicModule} from "ionic-angular";
import {CustomFooterComponent} from './custom-footer/custom-footer';
import {CustomHeaderComponent} from './custom-header/custom-header';
import {ProjectsHeaderComponent} from "./projects-header/projects-header";
import {PreparationSegmentsComponent} from "./preparation-segments/preparation-segments";
import {InterviewSegmentComponent} from './interview-segment/interview-segment';
import {TimerComponent} from './timer/app.component';
import {AnalysisSegmentComponent} from './analysis-segment/analysis-segment';
import {TimelineComponent} from './timeline/timeline';
import {CircleProgressComponent, CircleProgressOptions} from "./progress-circle/progress-circle";

@NgModule({
	declarations: [
    CustomFooterComponent,
    ProjectsHeaderComponent,
    PreparationSegmentsComponent,
    CustomHeaderComponent,
    InterviewSegmentComponent,
    TimerComponent,
    AnalysisSegmentComponent,
    TimelineComponent,
    CircleProgressComponent
  ],
	imports: [
    IonicModule],
	exports: [
    CustomFooterComponent,
    PreparationSegmentsComponent,
    ProjectsHeaderComponent,
    CustomHeaderComponent,
    InterviewSegmentComponent,
    TimerComponent,
    AnalysisSegmentComponent,
    TimelineComponent,
    CircleProgressComponent
  ],
  providers: [
    CircleProgressOptions
  ]
})
export class ComponentsModule {}



