import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from "@ngx-translate/core";
import { NavController, ViewController } from "ionic-angular";
import { upComingInterviews, finishedInterviews } from "../../util/constants";

@Component({
  selector: 'interview-segment',
  templateUrl: 'interview-segment.html'
})
export class InterviewSegmentComponent implements OnInit {

  @Input() pageTitle: string;
  @Input() tab: { name: string, value: number };
  @Output() filter = new EventEmitter<any>();

  private tabName: string;

  private upComingInterviews = upComingInterviews;
  private finishedInterviews = finishedInterviews;

  private tabNameUpComingInterviews: string;
  private tabNameFinishedInterviews: string;

  constructor(private navCtrl: NavController, private viewCtrl: ViewController,
              private translate: TranslateService) {
    this.translate.get('tabs.title.upComingInterview').take(1).subscribe(value => this.tabNameUpComingInterviews = value);
    this.translate.get('tabs.title.finishedInterviews').take(1).subscribe(value => this.tabNameFinishedInterviews = value);
  }

  public ngOnInit(): void {
    this.tabName = this.tab.name;
  }

  public selectTab(isFinished: boolean) {
    this.filter.emit({
      filter: isFinished,
    });
  }
}



