import { Component, Input } from "@angular/core";
import { NavController } from "ionic-angular";
import { AuthProvider } from "../../providers/auth/auth";
import { LoginPage } from "../../pages/login/login";

@Component({
  selector: 'projects-header',
  templateUrl: 'projects-header.html'
})
export class ProjectsHeaderComponent {
  @Input() pageTitle: string;

  constructor(private navCtrl: NavController, private authProvider: AuthProvider) {
  }

  logout() {
    try {
      this.authProvider.logout().then(success => {
        this.navCtrl.setRoot(LoginPage);
      })
    }
    catch (e) {
      console.log(e);
    }
  }
}



