import { EventEmitter } from "@angular/core";

/**
 * Timer
 * Manage the implicit timer.
 */
export class Timer {

  private _totaltime: number;
  private _timer;
  private update: EventEmitter<any>;
  private clock: EventEmitter<any>;

  /**
   * @name timestampTotal
   * @desc Get the timestamp of timer.
   * @returns {number}
   * @memberOf Timer
   */
  public get timestampTotal(): number { return this._totaltime * 10 }

  /**
   * @name constructor
   * @desc Constructor of class.
   * @param {EventEmitter<any>} update
   * @param {EventEmitter<any>} clock
   * @memberOf Timer
   */
  constructor(update: EventEmitter<any>, clock: EventEmitter<any>) {
    this.update = update;
    this.clock = clock;
  }

  /**
   * @name start
   * @desc Start a new timer.
   * @memberOf Timer
   */
  public start() {
    this._timer = setInterval(() => {
      // Increments the timer every O.O1 sec
      this._totaltime++;

      // Update parent value
      this.update.emit({
        timestampTotal: this.timestampTotal,
      });

      // Send a mark every 10 sec
      if(this.timestampTotal % 10000 === 0) {
        this.clock.emit({
          time: this.timestampTotal,
        });
      }
    }, 10);
  }

  /**
   * @name stop
   * @desc Pause the timer.
   * @memberOf Timer
   */
  public stop() {
    clearInterval(this._timer);
  }

  /**
   * @name reset
   * @desc Reset the timer.
   * @memberOf Timer
   */
  public reset() {
    this._totaltime = 0;

    // Send reset to parent
    this.update.emit({
      timestampTotal: this.timestampTotal,
      reset: true,
    });
  }

  /**
   * @name setTotalTime
   * @desc Set the totalTime.
   * @param {number} newTimer
   * @memberOf Timer
   */
  public setTotalTime(newTimer: number) {
    this._totaltime = newTimer;
  }
}



