import { Component, EventEmitter, Input, Output } from '@angular/core';

/**
 * TimelineComponent
 * Manage and display the timeline.
 * Methods here for print current/total timers.
 */
@Component({
  selector: 'timeline',
  templateUrl: 'timeline.html'
})
export class TimelineComponent {

  @Input() flags;
  @Input() marks;
  @Input() playedTime;
  @Input() totalTime;
  @Output() refresh = new EventEmitter<any>();

  /**
   * @name sendSelectedFlag
   * @desc Send the selected flag to parent.
   * @param {number} id: the id of selected flag.
   * @memberOf TimelineComponent
   */
  private sendSelectedFlag(id: number) {
    this.refresh.emit({
      selectedFlag: id,
    })
  }

  /**
   * @name getFormattedTime
   * @desc Return formatted time "0:00:00".
   * @param {number} time: the timestamp to convert.
   * @returns {string} the formatted time as a string.
   * @memberOf TimelineComponent
   */
  public getFormattedTime(time: number) {
    return TimelineComponent.getMinutes(time) + ":"
      + TimelineComponent.getSeconds(time) + ":"
      + TimelineComponent.getCentiSeconds(time);
  }

  /**
   * @name getSeconds
   * @desc Return seconds / 100.
   * @param {number} totalTime: the timestamp to exploit.
   * @returns {string} 1/100 seconds as a string.
   * @memberOf TimelineComponent
   */
  private static getCentiSeconds(totalTime: number) {
    return TimelineComponent.pad2(Math.floor((totalTime % 1000) / 10));
  }

  /**
   * @name getMinutes
   * @desc Return seconds.
   * @param {number} totalTime: the timestamp to exploit.
   * @returns {string} seconds as a string.
   * @memberOf TimelineComponent
   */
  private static getSeconds(totalTime: number) {
    return TimelineComponent.pad2(TimelineComponent.floor((totalTime / 1000) % 60));
  }

  /**
   * @name getHours
   * @desc Return minutes.
   * @param {number} totalTime: the timestamp to exploit.
   * @returns {string} minutes as a string.
   * @memberOf TimelineComponent
   */
  private static getMinutes(totalTime: number) {
    return TimelineComponent.floor(totalTime / 60000);
  }

  /**
   * @name floor
   * @desc Floor a number.
   * @param {number} number: the number to floor.
   * @returns {number} the floored number.
   * @memberOf TimelineComponent
   */
  private static floor(number: number) {
    return Math.floor(number);
  }

  /**
   * @name pad2
   * @desc Return formatted string number OO.
   * @param {number} number: the number to pad.
   * @returns {string} the padded number.
   * @memberOf TimelineComponent
   */
  private static pad2(number: number) {
    return (number < 10 ? '0' : '') + number;
  }
}



