import {AngularFirestore} from "angularfire2/firestore";
import {Objective} from "../../model/Objective";
import {Observable} from "rxjs/Observable";
import * as firebase from "firebase/app";
import "rxjs/add/operator/first";
import DocumentReference = firebase.firestore.DocumentReference;
import { Injectable } from "@angular/core";
import { pathObjectives, replaceId } from "../../util/constants";

@Injectable()
export class ObjectivesService {

  constructor(private firestore: AngularFirestore) {
  }

  public create(objective: Objective, idProject: string): Promise<DocumentReference> {
    // Adds the objective to the collection 'objectives' in project
    return this.firestore.collection<Objective>(ObjectivesService.getPath(idProject)).add({...objective});
  }

  public getObjectives(idProject: string): Observable<Objective[]> {
    return this.firestore.collection<Objective>(ObjectivesService.getPath(idProject))
      .snapshotChanges().map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data() as Objective;
          const id = a.payload.doc.id;
          return {id, ...data};
        });
      });
  }

  public getOrderedObjectives(idProject: string): Observable<Objective[]> {
    return this.firestore.collection<Objective>(ObjectivesService.getPath(idProject), ref => ref.orderBy('order'))
      .snapshotChanges().map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data() as Objective;
          const id = a.payload.doc.id;
          return {id, ...data};
        });
      });
  }

  public findObjective(idProject: string, name: string): Observable<Objective[]> {
    return this.firestore.collection(ObjectivesService.getPath(idProject), ref => ref.where('name', '==', name)).snapshotChanges()
      .map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data() as Objective;
          const id = a.payload.doc.id;
          return {id, ...data};
        });
      });
  }

  public update(id: string, idProject: string, data: Partial<Objective>): Promise<void> {
    return this.firestore.doc<Objective>(`${ObjectivesService.getPath(idProject)}/${id}`).update(data);
  }

  public remove(id: string, idProject: string): Promise<void> {
    return this.firestore.doc<Objective>(`${ObjectivesService.getPath(idProject)}/${id}`).delete();
  }

  private static getPath(idProj: string): string {
    return pathObjectives.replace(replaceId, idProj);
  }
}



