import { Component, OnDestroy, OnInit } from "@angular/core";
import { AlertController, NavController, NavParams } from "ionic-angular";
import { Project} from "../../model/Project";
import { preparation, questionCreator } from "../../util/constants";
import { ObjectivesService } from "../../providers/persistence/objectives.service";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { Subscription } from "rxjs/Subscription";
import { ViewService } from "../../providers/view/view.service";
import { TranslateService } from "@ngx-translate/core";
import "rxjs/add/operator/take";
import { QuestionService } from "../../providers/persistence/question.service";
import { InterviewFlowPage } from "../interview-flow/interview-flow";
import { Objective } from "../../model/Objective";
import { Question } from "../../model/Question";

@Component({
  selector: 'question-creator',
  templateUrl: 'question-creator.html'
})
export class QuestionCreatorPage implements OnInit, OnDestroy {
  private pageTitle: string;
  private pageSubTitle: string;
  private project: Project;
  private tab = preparation;
  private subTab = questionCreator;

  private objectives: BehaviorSubject<Objective[]> = new BehaviorSubject<Objective[]>([]);
  private questions: BehaviorSubject<Question[]> = new BehaviorSubject<Question[]>([]);
  private questionsTexts: string[] = [];
  private editMode: number = -1;
  private questionToAdd: string;

  private subs: Subscription[] = [];
  private adding = false;
  private message: string;
  private messageSuccessObjective: string;
  private newObjective: string;
  private messageSuccessQuestions: string;
  private messageFailQuestions: string;

  private edit: boolean[][];
  private alertTitleRemove: string;
  private alertMessageRemoveLastQuestion: string;
  private alertMessageRemoveNotLast: string;
  private alertBtnCancel: string;
  private alertBtnConfirm: string;
  private messageRemoveSuccess: string;
  private messageRemoveFail: string;
  constructor(private navCtrl: NavController, private navParams: NavParams, private objectiveService: ObjectivesService,
              private viewService: ViewService, private translate: TranslateService,
              private questionService: QuestionService, private alertCtrl: AlertController) {
    this.project = this.navParams.get("project");
    this.translate.get('tabs.title.preparation').take(1).subscribe(value => this.pageTitle = value);
    this.translate.get('question.creator.toast.error.missingQuestion').take(1)
      .subscribe(value => this.message = value);
    this.translate.get('question.creator.toast.success.createdObjective').take(1)
      .subscribe(value => this.messageSuccessObjective = value);
    this.translate.get('question.creator.toast.success.createdQuestions').take(1)
      .subscribe(value => this.messageSuccessQuestions = value);
    this.translate.get('question.creator.toast.error.createdQuestions').take(1)
      .subscribe(value => this.messageFailQuestions = value);
    this.translate.get('tabs.title.questionCreator').take(1).subscribe(value => this.pageSubTitle = value);

    this.translate.get('question.creator.alert.title').take(1).subscribe(value => this.alertTitleRemove = value);
    this.translate.get('question.creator.alert.messageLastQuestion').take(1)
      .subscribe(value => this.alertMessageRemoveLastQuestion = value);
    this.translate.get('question.creator.alert.messageNotLastQuestion').take(1)
      .subscribe(value => this.alertMessageRemoveNotLast = value);
    this.translate.get('question.creator.alert.button.confirm').take(1)
      .subscribe(value => this.alertBtnConfirm = value);
    this.translate.get('question.creator.alert.button.cancel').take(1).subscribe(value => this.alertBtnCancel = value);

    this.translate.get('question.creator.toast.success.removedQuestion').take(1)
      .subscribe(value => this.messageRemoveSuccess = value);
    this.translate.get('question.creator.toast.error.removedQuestion').take(1)
      .subscribe(value => this.messageRemoveFail = value);

  }

  public ngOnInit() {
    this.subs.push(
      this.objectiveService.getOrderedObjectives(this.project.id).subscribe(value => {
        this.objectives.next(value);
        this.editMode = -1;
      }),
      this.questionService.getOrderedQuestions(this.project.id).subscribe(value => {
        let tmpDisplay = [];
        for (let obj of this.objectives.getValue()) {
          let row = [];
          let numberQuestions = this.getQuestionsSizeByObjective(obj.id);
          for (let i = 0; i < numberQuestions; i++) {
            row.push(false);
          }
          tmpDisplay.push(row);
        }

        this.edit = tmpDisplay;
        this.questions.next(value);
      })
    );
  }

  public ngOnDestroy() {
    this.subs.forEach(sub => {
      sub.unsubscribe();
    });
  }

  public get createMode(): boolean {
    return this.questions.getValue().length == 0;
  }

  public get canEdit(): boolean {
    return this.project.preparation;
  }

  public getQuestionsSizeByObjective(idObjective: string): number {
    return this.questions.getValue().filter(value => value.objectifId == idObjective).length;
  }

  public getQuestionByObjective(idObjective: string): Question[] {
    return this.questions.getValue().filter(value => value.objectifId == idObjective);
  }

  public saveQuestions() {
    if (this.questionsTexts.length != this.objectives.getValue().length) {
      this.viewService.showToast(this.message);
      return;
    }

    this.questionService.createQuestions(this.questionsTexts, this.objectives.getValue(), this.project.id)
      .then(() => {
        this.viewService.showToast(this.messageSuccessQuestions);
      }).catch(() => {
      this.viewService.showToast(this.messageFailQuestions, 5000);
    });
  }

  public addObjective() {
    this.adding = true;
  }

  public detectEnterQuestion(keyCode: number, objectiveIndex: number) {
    if (keyCode == 13) {
      this.addQuestion(objectiveIndex);
    }
  }

  public addQuestion(objectiveIndex: number) {
    let objectiveId = this.objectives.getValue()[objectiveIndex].id;
    this.questionService.createOneQuestion(this.questionToAdd, objectiveId, this.project.id)
      .then(() => this.viewService.showToast(this.messageSuccessQuestions))
      .catch(reason => this.viewService.showToast(this.messageFailQuestions + ": " + reason));

    this.questionToAdd = "";
    this.editMode = -1;
  }

  public showAddQuestion(i: number) {
    this.questionToAdd = '';
    this.editMode = i;
  }

  public detectEnter(keyCode: number) {
    if (keyCode == 13) { // enter pressed
      this.adding = false;
      this.objectiveService.create(new Objective(this.newObjective, this.objectives.getValue().length), this.project.id)
        .then(() => {
          this.edit.push([]);
          this.viewService.showToast(this.messageSuccessObjective);
          this.newObjective = "";
        });
    }
  }

  public clear() {
    this.adding = false;
    this.newObjective = "";
  }

  public goToReorder() {
    this.navCtrl.push(InterviewFlowPage, {"project": this.project});
  }

  public updateQuestion(question: Question) {
    this.questionService.update(question.id, this.project.id, {question: question.question})
      .then(() => this.viewService.showToast(this.messageSuccessQuestions))
      .catch(() => this.viewService.showToast(this.messageFailQuestions));
  }

  public deleteQuestion(question: Question) {
    // lastQ: true if there will be no more questions after me, else false
    let lastQ: boolean = this.getQuestionsSizeByObjective(question.objectifId) == 1;

    this.alertCtrl.create({
      title: this.alertTitleRemove,
      message: lastQ ? this.alertMessageRemoveLastQuestion : this.alertMessageRemoveNotLast,
      buttons: [
        {
          text: this.alertBtnCancel,
          role: 'cancel',
          handler: () => {
          }
        }, {
          text: this.alertBtnConfirm,
          handler: () => {
            this.questionService.remove(question.id, this.project.id)
              .then(() => {
                if (lastQ) {
                  this.objectiveService.remove(question.objectifId, this.project.id)
                    .then(() => this.viewService.showToast(this.messageRemoveSuccess))
                    .catch(() => this.viewService.showToast(this.messageRemoveFail));
                } else {
                  this.viewService.showToast(this.messageRemoveSuccess);
                }
              }).catch(() => this.viewService.showToast(this.messageRemoveFail));

          }
        }
      ]
    }).present();
  }
}



