import { Component, OnInit } from "@angular/core";
import { AlertController, NavParams } from "ionic-angular";
import { Project } from "../../model/Project";
import { ObjectivesService } from "../../providers/persistence/objectives.service";
import { Observable } from "rxjs/Observable";
import { objectives } from "../../util/constants";
import { ViewService } from "../../providers/view/view.service";
import { TranslateService } from "@ngx-translate/core";
import { Subscription } from "rxjs/Subscription";
import { QuestionService } from "../../providers/persistence/question.service";
import { Objective } from "../../model/Objective";

@Component({
  selector: 'project-single',
  templateUrl: 'project-single.html'
})
export class ProjectSinglePage implements OnInit {


  ngOnInit(): void {
    this.subs.push(
      this.objectives.subscribe(value => {
        let hasQuestions: boolean[] = [];
        let edit: boolean[] = [];
        for (let i = 0; i < value.length; i++) {
          hasQuestions.push(true);
          edit.push(false);
        }
        this.hasQuestions = hasQuestions;
        this.edit = edit;

        for (let i = 0; i < value.length; i++) {
          this.questionService.existForObjective(value[i].id, this.project.id).share()
            .subscribe(exists => {
              this.hasQuestions[i] = exists;
            });
        }
      })
    )
  }
  private project: Project;
  private pageTitle: string = "View Project";
  private tab = objectives;

  private hasQuestions: boolean[];
  private subs: Subscription[] = [];
  private objectives: Observable<Objective[]>;
  private messageRemoveSuccess: string;
  private messageRemoveFail: string;
  private messageUpdateSuccess: string;
  private messageUpdateFail: string;
  private alertTitleRemove: string;
  private alertMessageRemoveHasQuestions: string;
  private alertMessageRemoveNoQuestions: string;
  private alertBtnCancel: string;
  private alertBtnConfirm: string;

  private edit: boolean[];

  constructor(private navParams: NavParams, private objectiveService: ObjectivesService,
              private viewService: ViewService, private translate: TranslateService,
              private questionService: QuestionService, private alertCtrl: AlertController) {
    this.project = this.navParams.get('project');

    this.objectives = this.objectiveService.getOrderedObjectives(this.project.id).share();

    this.translate.get('project.single.toast.success.objectiveDeleted').take(1)
      .subscribe(value => this.messageRemoveSuccess = value);
    this.translate.get('project.single.toast.error.objectiveDeleted').take(1)
      .subscribe(value => this.messageRemoveFail = value);
    this.translate.get('project.single.alert.btn.deleteConfirm').take(1)
      .subscribe(value => this.alertBtnConfirm = value);
    this.translate.get('project.single.alert.btn.cancel').take(1).subscribe(value => this.alertBtnCancel = value);
    this.translate.get('project.single.alert.title').take(1).subscribe(value => this.alertTitleRemove = value);
    this.translate.get('project.single.alert.messageHasQuestions').take(1)
      .subscribe(value => this.alertMessageRemoveHasQuestions = value);
    this.translate.get('project.single.alert.messageNoQuestions').take(1)
      .subscribe(value => this.alertMessageRemoveNoQuestions = value);
    this.translate.get('project.single.toast.success.objectiveUpdated').take(1)
      .subscribe(value => this.messageUpdateSuccess = value);
    this.translate.get('project.single.toast.error.objectiveUpdated').take(1)
      .subscribe(value => this.messageUpdateFail = value);
  }

  public get canEdit() {
    return this.project.preparation;
  }

  public remove(objectif: Objective, i: number) {
    let hasQ = this.hasQuestions[i];

    this.alertCtrl.create({
      title: this.alertTitleRemove,
      message: hasQ ? this.alertMessageRemoveHasQuestions : this.alertMessageRemoveNoQuestions,
      buttons: [
        {
          text: this.alertBtnCancel,
          role: 'cancel',
          handler: () => {

          }
        }, {
          text: this.alertBtnConfirm,
          handler: () => {
            this.objectiveService.remove(objectif.id, this.project.id)
              .then(() => {
                if (hasQ) {
                  this.questionService.removeAllFromObjective(objectif.id, this.project.id)
                    .then(() => this.viewService.showToast(this.messageRemoveSuccess))
                    .catch(() => this.viewService.showToast(this.messageRemoveFail));
                } else {
                  this.viewService.showToast(this.messageRemoveSuccess);
                }
              })
              .catch(() => this.viewService.showToast(this.messageRemoveFail));
          }
        }
      ]
    }).present();


  }

  public clickEdit(objectif: Objective, i: number) {
    this.edit[i] = true;
  }

  public doEdit(objectif: Objective, i: number) {
    this.objectiveService.update(objectif.id, this.project.id, {name: objectif.name})
      .then(value => {
        this.viewService.showToast(this.messageUpdateSuccess);
        this.edit[i] = false;
      }).catch(reason => this.viewService.showToast(this.messageUpdateFail + " " + reason));
  }
}



