import { AngularFirestore, DocumentChangeAction } from "angularfire2/firestore";
import { Project } from "../../model/Project";
import { Observable } from "rxjs/Observable";
import * as firebase from "firebase/app";
import { Injectable } from "@angular/core";
import { pathProjects } from "../../util/constants";
import DocumentReference = firebase.firestore.DocumentReference;

@Injectable()
export class ProjectService {

  constructor(private firestore: AngularFirestore) {
  }

  public create(project: Project): Promise<DocumentReference> {
    return this.firestore.collection<Project>(pathProjects).add({...project});
  }

  public getProject(id: string): Observable<Project | null> {
    return this.firestore.doc<Project>(`${pathProjects}/${id}`).valueChanges().share();
  }

  public getProjects(): Observable<Project[]> {
    return this.firestore.collection<Project>("projects")
      .snapshotChanges().map((actions: DocumentChangeAction[]) => {
        return actions.map((a: DocumentChangeAction) => {
          const project = a.payload.doc.data() as Project;
          const id = a.payload.doc.id;
          return {id, ...project};
        });
      });
  }

  public isProjectNameAvailable(name: string): Observable<boolean> {
    return this.getProjects().share()
      .map((projects: Project[]) => projects.filter(p => p.name === name))
      .map(projects => !projects.length)
  }

  public update(id: string, data: Partial<Project>): Promise<void> {
    return this.firestore.doc(`${pathProjects}/${id}`).update(data);
  }

  public remove(id: string): Promise<void> {
    return this.firestore.doc<Project>(`${pathProjects}/${id}`).delete();
  }
}



