import { Component, OnDestroy, OnInit } from "@angular/core";
import { NavController, NavParams } from "ionic-angular";
import { Project } from "../../model/Project";
import {finishedInterviews, interviews, upComingInterviews} from "../../util/constants";
import { TranslateService } from "@ngx-translate/core";
import "rxjs/add/operator/take";
import { InterviewsService } from "../../providers/persistence/interviews.service";
import { InterviewRecordPage } from "../interview-record/interview-record";
import { Subscription } from "rxjs/Subscription";
import { interviewStatus } from "../../util/enum";
import { Interview } from "../../model/Interview";

/**
 * Controller of interview list page.
 * This page display the list of interviews.
 */
@Component({
  selector: 'interview',
  templateUrl: 'interview.html'
})
export class InterviewPage implements OnInit, OnDestroy {

  private subscriptions: Array<Subscription>;

  private pageTitle: string;

  private project: Project;

  private tab = interviews;
  private subTab = upComingInterviews;

  private interviews: Array<Interview>;
  private filteredInterviews: Array<Interview>;

  /**
   * @name constructor
   * @desc Constructor init our values.
   * @param {NavParams} navParams
   * @param {NavController} navCtrl
   * @param {TranslateService} translate
   * @param {InterviewsService} interviewsService
   * @memberOf InterviewPage
   */
  constructor(private navParams: NavParams, private navCtrl: NavController,
              private translate: TranslateService, private interviewsService: InterviewsService) {
    this.project = this.navParams.get("project");
    this.translate.get('interview.title').take(1).subscribe(value => this.pageTitle = value);

    this.interviews = [];
    this.filteredInterviews = [];
  }

  /**
   * @name ngOnInit
   * @desc On init, subscribe to observable.
   * @memberOf InterviewPage
   */
  public ngOnInit() {
    // Subcriptions
    this.subscriptions = new Array<Subscription>();
    this.subscriptions.push(this.interviewsService.getInterviews(this.project.id).subscribe((data) => {
      this.interviews = data;
      if(this.subTab === upComingInterviews){
        this.filterList(false);
      } else {
        this.filterList(true);
      }
    }));
  }

  /**
   * @name ngOnDestroy
   * @desc On destroy, unsubscribe to all subscribed observable.
   * @memberOf InterviewPage
   */
  public ngOnDestroy() {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

  /**
   * @name filterList
   * @desc Filter the list of interviews.
   * @param {boolean} filter
   * @memberOf InterviewPage
   */
  public filterList(filter: boolean): void {
    if (filter) {
      this.subTab = finishedInterviews;
      this.filteredInterviews = this.interviews.filter(e => e.status === interviewStatus.done);
    } else {
      this.subTab = upComingInterviews;
      this.filteredInterviews = this.interviews.filter(e => e.status !== interviewStatus.done);
    }
  }

  /**
   * @name goToInterview
   * @desc Go to interview's recording page.
   * @param {Interview} interview
   * @memberOf InterviewPage
   */
  public goToInterview(interview: Interview): void {
    this.navCtrl.push(InterviewRecordPage, {"project": this.project, "interviewId": interview.id});
  }
}



