import { Injectable } from '@angular/core';
import * as firebase from 'firebase/app';
import {AngularFirestore} from "angularfire2/firestore";
import 'firebase/messaging';
import Messaging = firebase.messaging.Messaging;
import {AuthProvider} from "../auth/auth";
import {Subscription} from "rxjs/Subscription";


interface Message {
  notification: {
    title: string,
    body: string,
    icon: string
  }
}


@Injectable()
export class MessagingProvider {

  messaging: Messaging = firebase.messaging();
  registration: ServiceWorkerRegistration;

  userSubscr: Subscription;

  constructor(private firestore: AngularFirestore, private auth: AuthProvider) {

    console.log("MessagingProvider initialized");

    navigator.serviceWorker.getRegistration().then((registration) => {
      this.registration = registration;
      this.messaging.useServiceWorker(registration);
    });
  }

  initSubscription(){
    this.userSubscr = this.auth.user$
      .filter(user => !!user) // filter null
      .take(1) // take first real user
      .subscribe((user) => {
        if (user) {
          console.log(`Subscribe on messages : ${user.displayName}`);
          this.getPermission(user);
          this.monitorRefresh(user);
          this.receiveMessages();
        }
      });
  }

  // get permission to send messages
  getPermission(user) {
    this.messaging.requestPermission()
      .then(() => {
        console.log('Notification permission granted.');
        return this.messaging.getToken()
      })
      .then(token => {
        console.log(`token ${+token}`);
        this.saveToken(user, token)
      })
      .catch((err) => {
        console.log('Unable to get permission to notify.', err);
      });
  }

  // Listen for token refresh
  monitorRefresh(user) {
    this.messaging.onTokenRefresh(() => {
      this.messaging.getToken()
        .then(refreshedToken => {
          console.log('Token refreshed.');
          this.saveToken(user, refreshedToken)
        })
        .catch( err => console.log(err, 'Unable to retrieve new token') )
    });
  }

  // save the permission token in firestore
  private saveToken(user, token): void {

    const currentTokens = user.fcmTokens || { };
    // If token does not exist in firestore, update db
    if (!currentTokens[token]) {
      console.log(`token to save ${token}`);
      const userRef = this.firestore.collection('users').doc(user.uid);
      const tokens = { ...currentTokens, [token]: true };
      userRef.update({ fcmTokens: tokens });
    }
  }

  // used to show message when app is open
  receiveMessages() {
    this.messaging.onMessage((payload: Message) => {
      console.log(`Message received. ${payload}`);

      if(!payload.notification)return;

      const notificationTitle = payload.notification.title;
      const notificationOptions = {
        body: payload.notification.body,
        icon: payload.notification.icon
      };

      this.registration.showNotification(notificationTitle, notificationOptions);
    });
  }

  ngOnDestroy() {
    console.log("unsubscribe messaging subscriptions");
    this.userSubscr.unsubscribe();
  }

}



