import { Component, EventEmitter, Input, Output, OnInit } from '@angular/core';
import { Timer } from './timer';
import { State } from './state';

/**
 * TimerComponent
 * Manage the buttons and implicit timers.
 */
@Component({
  selector: 'timer',
  templateUrl: './app.component.html',
})
export class TimerComponent implements OnInit {

  /*
   * Events sent to parent
   */
  @Output() update = new EventEmitter<any>();
  @Output() play_stop = new EventEmitter<any>();
  @Output() clock = new EventEmitter<any>();
  @Output() init = new EventEmitter<any>();

  private _timer: Timer = new Timer(this.update, this.clock);
  private _state: State = new State();

  @Input() time = this._timer.timestampTotal;
  @Input() icon;

  /**
   * @name ngOnInit
   * @desc Called on init of component.
   * @memberOf TimerComponent
   */
  public ngOnInit() {
    // Send component to parent
    this.init.emit({
      timerComponent: this,
    })
  }

  /**
   * @name play
   * @desc Start the timer.
   * @memberOf TimerComponent
   */
  public play() {
    this._timer.setTotalTime(this.time / 10);

    this._timer.start();
    this._state.setPlay();

    // True for send PLAY
    this.play_stop.emit({
      value: true,
      timerComponent: this
    })
  }

  /**
   * @name stop
   * @desc Pause the timer.
   * @memberOf TimerComponent
   */
  public stop() {
    this._timer.stop();
    this._state.setStop();

    // False for send STOP
    this.play_stop.emit({
      value: false,
    })
  }

  /**
   * @name backward
   * @desc Reset the component.
   * @memberOf TimerComponent
   */
  public backward() {
    this._timer.reset();
    this._state.setBackward();
  }
}



