import { Component, OnDestroy, OnInit } from "@angular/core";
import { AlertController, ModalController, NavParams } from "ionic-angular";
import { Project } from "../../model/Project";
import { participantsPanel, preparation } from "../../util/constants";
import { TranslateService } from "@ngx-translate/core";
import "rxjs/add/operator/take";
import { ParticipantService } from "../../providers/persistence/participant.service";
import { DatePickerProvider } from 'ionic2-date-picker';
import { ViewService } from "../../providers/view/view.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ImageService } from "../../providers/persistence/image.service";
import * as firebase from "firebase/app";
import uuid from "uuid-random";
import { DisplayParticipant } from "../../model/DisplayParticipant";
import { Participant } from "../../model/Participant";
import UploadTaskSnapshot = firebase.storage.UploadTaskSnapshot;
import { AmazingTimePickerService } from "amazing-time-picker";
import { InterviewsService } from "../../providers/persistence/interviews.service";
import {Interview} from "../../model/Interview";

@Component({
  selector: 'participant-panel',
  templateUrl: 'participant-panel.html'
})
export class ParticipantPanelPage implements OnInit, OnDestroy {
  private pageTitle: string;
  private pageSubtitle: string;
  private project: Project;
  private tab = preparation;
  private subTab = participantsPanel;
  private addingParticipant: boolean;
  private file: File;

  private newParticipant: Participant;
  private newParticipantForm: FormGroup;

  private progress: number;
  private uploadingImage: boolean;

  private participants: Participant[] = [];
  private display: DisplayParticipant[] = [];
  private alertTitleRemove: string;
  private alertMessageRemove: string;
  private alertBtnConfirm: string;
  private alertBtnCancel: string;
  private messageDeleted: string;
  private messageDeletedFail: string;
  private updateSuccess: string;
  private updateFail: string;
  private messageUploadPaused: string;
  private interviewCreated: string;
  private interviewFailed: string;

  constructor(private navParams: NavParams, private translate: TranslateService,
              private participantService: ParticipantService, private interviewsService: InterviewsService,
              private modalCtrl: ModalController,
              private datePickerProvider: DatePickerProvider, private viewService: ViewService,
              formBuilder: FormBuilder, private imageService: ImageService, private alertCtrl: AlertController,
              private atp: AmazingTimePickerService) {
    this.project = this.navParams.get("project");
    this.translations();

    this.newParticipant = new Participant();

    this.newParticipantForm = formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      jobTitle: ['', Validators.required],
      company: [''],
      email: ['', Validators.compose([Validators.required, Validators.email])],
      phone: [''],
      expertise: [''],
      city: ['', Validators.required],
      rdv: [''],
      notes: [''],
      proto: ['']
    });
  }

  private translations() {
    this.translate.get('tabs.title.preparation').take(1).subscribe(value => this.pageTitle = value);
    this.translate.get('tabs.title.participantsPanel').take(1).subscribe(value => this.pageSubtitle = value);
    this.translate.get('participants.modal.remove.message').take(1).subscribe(value => this.alertMessageRemove = value);
    this.translate.get('participants.modal.remove.title').take(1).subscribe(value => this.alertTitleRemove = value);
    this.translate.get('participants.modal.remove.confirm').take(1).subscribe(value => this.alertBtnConfirm = value);
    this.translate.get('participants.modal.remove.cancel').take(1).subscribe(value => this.alertBtnCancel = value);
    this.translate.get('participants.message.deleteSuccess').take(1).subscribe(value => this.messageDeleted = value);
    this.translate.get('participants.message.deleteFail').take(1).subscribe(value => this.messageDeletedFail = value);
    this.translate.get('participants.message.updateSuccess').take(1).subscribe(value => this.updateSuccess = value);
    this.translate.get('participants.message.updateFail').take(1).subscribe(value => this.updateFail = value);
    this.translate.get('participants.message.uploadPaused').take(1)
      .subscribe(value => this.messageUploadPaused = value);
    this.translate.get('participants.message.interviewCreated').take(1).subscribe(value => this.interviewCreated = value);
    this.translate.get('participants.message.interviewFailed').take(1).subscribe(value => this.interviewFailed = value);
  }

  private initDisplay(length: number) {
    let display = [];
    for (let i = 0; i < length; i++) {
      display.push(new DisplayParticipant());
    }
    this.display = display;
  }

  ngOnInit() {
    this.participantService.getParticipants(this.project.id).share().subscribe(value => {
      if (value.length !== this.participants.length) {
        // We change the display only if there is a participant added or deleted
        this.initDisplay(value.length);
      }
      this.participants = value;
    });

  }

  public launchDatePicker() {
    let datePickerOption = {minimumDate: new Date()};
    const dateSelected = this.datePickerProvider.showCalendar(this.modalCtrl, datePickerOption);
    dateSelected.subscribe((date: Date) => {
      this.openTimePicker(date);
    });
  }

  private openTimePicker(date: Date) {
    const timePicker = this.atp.open();
    timePicker.afterClose().subscribe(time => {
      let hm = time.split(':').map(v => +v);
      date.setHours(hm[0], hm[1], 0);
      this.newParticipant.rdv = date;
    })
  }

  ngOnDestroy() {

  }

  private static setUpload(display: DisplayParticipant) {
    if (display)
      display.uploading = false;
  }

  public fileChanged($event, participant: Participant, save: boolean = false, display: DisplayParticipant = null) {
    if (!$event.target.files || !$event.target.files[0]) return;
    this.uploadingImage = true;
    this.file = $event.target.files[0];
    let uploadTask = this.imageService.saveImage(this.project.id, participant.id, this.file);
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, (snapshot: UploadTaskSnapshot) => {
      // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
      this.progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
      switch (snapshot.state) {
        case firebase.storage.TaskState.PAUSED: // or 'paused'
          this.viewService.showToast(this.messageUploadPaused);
          break;
        case firebase.storage.TaskState.RUNNING: // or 'running'
          break;
      }

    }, (error: any) => {
      // A full list of error codes is available at
      // https://firebase.google.com/docs/storage/web/handle-errors
      switch (error.code) {
        case 'storage/unauthorized':
          // User doesn't have permission to access the object
          break;
        case 'storage/canceled':
          // User canceled the upload
          break;
        case 'storage/unknown':
          // Unknown error occurred, inspect error.serverResponse
          break;
      }
      this.uploadingImage = false;

    }, () => {
      // Upload completed successfully, now we can get the download URL
      participant.urlImage = uploadTask.snapshot.downloadURL;
      if (save) {
        this.update(participant, 'image');
        ParticipantPanelPage.setUpload(display);
      }
      this.uploadingImage = false;
    });
  }

  public clickFinished() {
    if (this.newParticipantForm.valid) {
      this.addParticipant();
    }
  }

  public addParticipant() {
    this.participantService.createWithId(this.newParticipant, this.project.id).then(() => {
      this.viewService.showToast("Haha");
      this.addingParticipant = false;
      this.newParticipant = new Participant();
    }).catch(reason => {
      this.viewService.showToast("Too bad: " + reason);
    });
  }

  public sendEmail(email: string, date: Date) {
    // TODO see https://medium.com/@markgoho/create-a-contact-form-in-angular-using-cloud-functions-for-firebase-5e390bdf5600
    // alert("TODO send an email to participant (" + email + ")");
    window.open(
      `mailto:${email}?subject=Schedule RDV for project ${this.project.name}&body=Are you available on ${date}?`,
      "_self");
  }

  public schedule() {
    this.newParticipant.interviewStatus = 'waiting';
    this.sendEmail(this.newParticipant.email, this.newParticipant.rdv);
  }

  public scheduleRdv(participant: Participant) {
    this.sendEmail(participant.email, participant.rdv);
    participant.interviewStatus = 'waiting';
    this.update(participant, "interviewStatus");
  }


  public clickContact(participant: Participant, type: 'email' | 'phone') {
    if (type == 'email') {
      console.log(`contacting ${participant.email}`);
    } else {
      console.log(`contacting ${participant.phone}`);
    }
  }

  public update(participant: Participant,
                property: 'phone' | 'email' | 'name' | 'job' | 'city' | 'level' | 'proto' | 'note' | 'interviewStatus' | 'image') {
    let data: Partial<Participant> = {};
    switch (property) {
      case "phone":
        data = {phone: participant.phone ? participant.phone : null};
        break;
      case "email":
        data = {email: participant.email ? participant.email : null};
        break;
      case "name":
        data = {
          firstName: participant.firstName ? participant.firstName : null,
          lastName: participant.lastName ? participant.lastName : null
        };
        break;
      case "job":
        data =
          {job: participant.job ? participant.job : null, company: participant.company ? participant.company : null};
        break;
      case "city":
        data = {city: participant.city ? participant.city : null};
        break;
      case "level":
        data = {level: participant.level ? participant.level : null};
        break;
      case "proto":
        data = {proto: participant.proto ? participant.proto : null};
        break;
      case "note":
        data = {notes: participant.notes ? participant.notes : null};
        break;
      case "interviewStatus":
        data = {interviewStatus: participant.interviewStatus ? participant.interviewStatus : null};
        break;
      case "image":
        data = {urlImage: participant.urlImage ? participant.urlImage : null};
        break;
    }

    this.participantService.update(participant.id, data, this.project.id)
      .then(() => this.viewService.showToast(this.updateSuccess))
      .catch(reason => this.viewService.showToast(this.updateFail));
  }

  public showRemoveParticipant(participant: Participant) {
    this.alertCtrl.create({
      title: this.alertTitleRemove,
      message: this.alertMessageRemove + "\n" + participant.firstName + " " + participant.lastName,
      buttons: [
        {
          text: this.alertBtnCancel,
          role: 'cancel',
          handler: () => {

          }
        }, {
          text: this.alertBtnConfirm,
          handler: () => {
            this.participantService.remove(participant.id, this.project.id)
              .then(() => this.viewService.showToast(this.messageDeleted))
              .catch(reason => this.viewService.showToast(this.messageDeletedFail));
          }
        }
      ]
    }).present();
  }

  public showAddParticipant() {
    this.addingParticipant = true;
    this.newParticipant.id = uuid();
  }

  public cancelAddParticipant() {
    this.addingParticipant = false;
    if (this.newParticipant.urlImage) {
      // delete image saved
      this.imageService.deleteImage(this.project.id, this.newParticipant.id);
    }
    this.newParticipant = new Participant();
  }

  public createInterview(participant: Participant) {
    if(participant.firstName && participant.lastName && participant.job) {
      this.interviewsService.create(new Interview(`${participant.firstName} ${participant.lastName}`,
        participant.job,
        participant.level ? participant.level : "",
        participant.rdv ? participant.rdv : new Date(Date.now())), this.project.id)
        .then(() => {
        this.viewService.showToast(this.interviewCreated);
      });
    } else {
      this.viewService.showToast(this.interviewFailed);
    }
  }
}



