import { Component, OnDestroy, OnInit } from "@angular/core";
import { NavController, NavParams } from "ionic-angular";
import { Project } from "../../model/Project";
import { analysis } from "../../util/constants";
import { TranslateService } from "@ngx-translate/core";
import "rxjs/add/operator/take";
import { Observable } from "rxjs/Observable";
import { Subscription } from "rxjs/Subscription";
import { InterviewsService } from "../../providers/persistence/interviews.service";
import { AnalysisTextPage } from "../analysis-text/analysis-text";
import { analysisStatus } from "../../util/enum";
import { Interview } from "../../model/Interview";

/**
 * Controller of analysis list page.
 * This page display the list on interview for analyse.
 */
@Component({
  selector: 'analysis',
  templateUrl: 'analysis.html'
})
export class AnalysisPage implements OnInit, OnDestroy {

  private subscriptions: Subscription[];

  private pageTitle: string;
  private project: Project;

  private interviews: Observable<Interview[]>;
  private scheduledAnalysis: Interview[];
  private doneAnalysis: Interview[];

  private tab = analysis;

  /**
   * @name constructor
   * @desc Constructor init our values.
   * @param {NavParams} navParams
   * @param {NavController} navCtrl
   * @param {TranslateService} translate
   * @param {InterviewsService} interviewsService
   * @memberOf AnalysisPage
   */
  constructor(private navParams: NavParams, private navCtrl: NavController,
              private translate: TranslateService, private interviewsService: InterviewsService) {
    this.project = this.navParams.get("project");
    this.translate.get('analysis.title.main').take(1).subscribe(value => this.pageTitle = value);

    this.scheduledAnalysis = new Array<Interview>();
    this.doneAnalysis = new Array<Interview>();
  }

  /**
   * @name ngOnInit
   * @desc On init, subscribe to observable.
   * @memberOf AnalysisPage
   */
  public ngOnInit() {
    // Get data
    this.interviews = this.interviewsService.getInterviews(this.project.id);

    // Subscriptions
    this.subscriptions = new Array<Subscription>();
    this.subscriptions.push(this.interviews.subscribe((interviews) => {
      this.scheduledAnalysis = [];
      this.doneAnalysis = [];

      interviews.forEach((e) => {
        if(e.analysis === analysisStatus.scheduled) this.scheduledAnalysis.push(e);
        else if (e.analysis === analysisStatus.done) this.doneAnalysis.push(e);
      });
    }));
  }

  /**
   * @name ngOnDestroy
   * @desc On destroy, unsubscribe to all subscribed observable.
   * @memberOf AnalysisPage
   */
  public ngOnDestroy() {
    this.subscriptions.forEach((e) => e.unsubscribe());
  }

  /**
   * @name goToAnalysisInterview
   * @desc Go to AnalysisText page.
   * @param interviewId: the id of interview.
   * @memberOf AnalysisPage
   */
  public goToAnalysisInterview(interviewId): void {
    this.navCtrl.push(AnalysisTextPage, {"project": this.project, "interviewId": interviewId})
  }
}



