import {Injectable} from "@angular/core";
import * as firebase from "firebase/app";
import {Utils} from "../../util/utils";

@Injectable()
export class ImageService {
  constructor() {
  }

  public saveImage(idProject: string, idParticipant: string, imageFile: File): firebase.storage.UploadTask {
    return firebase.storage().ref(Utils.imagePath(idProject, idParticipant)).put(imageFile);
  }

  public deleteImage(idProject: string, idParticipant: string,): Promise<any> {
    return firebase.storage().ref(Utils.imagePath(idProject, idParticipant)).delete();
  }
}



