import { Injectable } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase/app';
import {AngularFirestore, AngularFirestoreDocument} from "angularfire2/firestore";
import {User} from "../../model/User";
import {BehaviorSubject} from "rxjs/BehaviorSubject";
import {Unsubscribe} from "firebase/app";

@Injectable()
export class AuthProvider {

  public user$: BehaviorSubject<User>;
  private isLogged: boolean;

  private onAuthUnsubscribe: Unsubscribe;

  public get getUserId() { return this.user$.getValue().uid; }

  constructor(private afAuth: AngularFireAuth, private firestore: AngularFirestore) {

    this.user$ = new BehaviorSubject(null);
    this.isLogged = false;

    this.onAuthUnsubscribe = firebase.auth().onAuthStateChanged((user) => {
      console.log("onAuthStateChanged");
      console.log(user?`User : ${user.displayName}`:'Not logged in');

      if (user) {
        this.isLogged = true;
        this.user$.next(user);
      } else {
        this.isLogged = false;
        this.user$.next(null);
      }
    });
  }

  loginWithGoogle():Promise<any> {
    console.log("call loginWithGoogle");
    return this.afAuth.auth.signInWithRedirect(new firebase.auth.GoogleAuthProvider());
  }

  logout():Promise<any> {
    return this.afAuth.auth.signOut();
  }
  isLoggedIn(): boolean {
    return this.isLogged;
  }

  async retrieveLogin(): Promise<any> {
    return await this.afAuth.auth.getRedirectResult().then((credential) => {
      if(credential.user){
        this.updateUserData(credential.user)
      }
    })
  }

  private updateUserData(user) {
    console.log("updateUserData");
    const userRef: AngularFirestoreDocument<any> = this.firestore.doc(`users/${user.uid}`);
    const data: User = {
      uid: user.uid,
      email: user.email,
      displayName: user.displayName,
      photoURL: user.photoURL
    };
    userRef.set(data)
      .then(() => console.log("user updated"))
      .catch(() => console.log("error on update"));
  }

  ngOnDestroy() {
    console.log("unsubscribe authentification subscriptions");
    this.onAuthUnsubscribe();
  }
}



